.promo {
	position: fixed;
	top:0;
	left:0;
	width:100%;
	z-index: 1000;
	background: cl(yellow);
	padding: rem(0 50px);
	height: rem(50px);
	font-size: rem(20px);
	text-align: center;
	display: flex;
	justify-content: center;
	align-items: center;
	gap:rem(10px);
	color:black;
	display: none;
	@include media-breakpoint-down(lg) {
		padding: rem(0 30px);
	}
	@include media-breakpoint-down(md) {
		font-size: rem(16px);
	}
	@include media-breakpoint-down(xs) {
		font-size: rem(13px);
		padding: 0 3.25rem 0 1.25rem;
	}
	&.show {
		display: flex;
	}
	&-close {
		position: absolute;
		top: rem(14px);
		right: rem(50px);
		width: rem(22px);
		height: rem(22px);
		overflow: hidden;
		text-indent: -999px;
		background: url(/img/svg/cancel.svg) no-repeat 50% 50%;
		background-size: contain;
		padding: 0;
		border:0;
		z-index: 10;
		@include media-breakpoint-down(lg) {
			right: 1.2rem;
		}
		@include media-breakpoint-down(xs) {
			right:1rem;
			top: rem(16px);
			width: rem(20px);
			height: rem(20px);
		}
	}
	&.show {
		& + {
			.header {
				top:rem(50px);
			}
		}
		& ~ {
			.banner {
				margin-top:rem(50px);
			}
		}
	}
}