.row:before, .row:after {display: none !important;}
* {
	margin:0;
	padding: 0;
}
html,
body {
	color: cl(text);
	//min-height: 100vh;
	background: cl(white);
	font-family: $font;
	line-height: 1.1;
	@include rem-baseline;
	font-size: calc(10.75px + 15 * ((100vw - 1200px) / 2048));
	@media screen and (min-width: 2049px) {
		font-size: calc(20px + 15 * ((100vw - 2049px) / 4096));
	}
	@include media-breakpoint-down(lg) {
		font-size: calc(12px + 4 * ((100vw - 576px) / 1199.98));
	}
	@include media-breakpoint-down(xs) {
		font-size: calc(13px + 2 * ((100vw - 320px) / 575.98));
	}
	@media (max-width: 400.98px){
		font-size: calc(11px + 2 * ((100vw - 320px)/ 575.98));
	}

}
img {
	vertical-align: top;
}
a {
	color: cl(primary);
	text-decoration: none;
	&:hover {
		color: cl(primary);
		text-decoration: underline;
	}
}
.overlay {
	padding-top: 0;
	position: fixed;
	top: 0;
	left: 0;
	right: 0;
	bottom: 0;
	overflow: hidden;
	z-index: 10;
	display:none;
	&:before {
		content: '';
		position: absolute;
		top: 0;
		left: 0;
		width: 100%;
		height: 100%;
		background-color: rgba(0, 0, 0, 0.5);
		z-index: 100;
	}
	&.show {
		display: block;
	}
}
.container {
	--size: 76.875rem;
	max-width: var(--size);
	&--small {
		--size: 800px;
	}
	&--big {
		--size: 85.625rem;
	}
}
.wrapper {
	padding: rem(0 190px);
}
.loader {
	position: fixed;
	left:0;
	top:0;
	width:100%;
	height: 100%;
	display: flex;
	justify-content: center;
	align-items: center;
	z-index: 9999;
	background: cl(white);
}
.title-tag {
	display: inline-block;
	vertical-align: top;
	background: #a6a7b9;
	color:white;
	text-transform: uppercase;
	font-size: rem(12px);
	line-height: rem(14px);
	font-weight: 700;
	letter-spacing: rem(.2px);
	border-radius: rem(3px);
	padding: rem(5px 10px);
	margin: rem(0 0 20px);
}
.trial {
	&-form {
		background: cl(white, .15);
		padding: rem(10px);
		border-radius: rem(3px);
		gap:rem(10px);
		display: flex;
		margin: rem(0 0 20px);
		@include media-breakpoint-down(xs) {
			flex-wrap: wrap;
		}
	}
	&-txt {
		width:60%;
		height: rem(50px);
		background: white;
		padding: rem(0 20px);
		font-family: $font;
		border-radius: rem(5px);
		background: cl(white);
		border:rem(1px solid cl(white));
		color:cl(text);
		@include placeholder(#6e6e6e);
		@include media-breakpoint-down(xs) {
			width:100%;
		}
	}
	.btn {
		width:40%;
		@include media-breakpoint-down(xs) {
			width:100%;
		}
	}
	&-btn {
		display: flex;
		align-items: center;
		gap:rem(30px);
		font-size: rem(15px);
		line-height: rem(18px);
		color: #9c9db1;
		letter-spacing: 0;
		@include media-breakpoint-down(xs) {
			gap:rem(20px);
		}
	}
	&-inner {
		padding: rem(30px 0 0);
		font-size: rem(16px);
		color:white;
		display: flex;
		align-items: center;
		gap:rem(13px);
		@include media-breakpoint-down(xs) {
			flex-wrap: wrap;
			justify-content: space-between;
			span {
				width:100%;
				text-align: center;
			}
		}
		.social-link {
			font-weight: 700;
			color:white;
			line-height: rem(38px);
			border-radius: rem(3px);
			border:1px solid cl(white, .5);
			width:rem(140px);
			text-align: center;
			display: flex;
			align-items: center;
			justify-content: center;
			gap:rem(15px);
			letter-spacing: 0;
			@include media-breakpoint-down(xs) {
				//width:100%;
				width:auto;
				padding: rem(0 12px);
			}
			&:hover {
				text-decoration: none;
				background: cl(white, .1);
			}
			&--g {
				i {
					color:cl(google);
				}
			}
			&--fb {
				i {
					color:cl(facebook);
				}
			}
			&--tw {
				i {
					color:cl(twitter);
				}
			}
			img {
				width:rem(20px);
				height: auto;
			}
			i {
				font-size: rem(18px);
			}
		}
	}
}
.link-top {
	display: none;
	position: fixed;
	right:rem(30px);
	bottom: rem(30px);
	z-index: 100;
	width: rem(50px);
	height: rem(50px);
	border-radius: 50%;
	font-size: rem(18px);
	color:white;
	background: cl(primary);
	box-shadow:rem(0 15px 29px cl(black, .44));
	transition: all .1s linear;
	&:hover {
		text-decoration: none;
		background: cl(secondary);
		color:white;
	}
	&.show {
		display: block;
	}

	@include media-breakpoint-down(lg) {
		right:1rem;
		bottom:1rem;
		width: rem(40px);
		height: rem(40px);
		box-shadow:rem(0 5px 16px cl(black, .4));
		font-size: rem(16px);
	}
	span {
		display: flex;
		justify-content: center;
		align-items: center;
		width:100%;
		height: 100%;
	}
}
