
.header {
	position: fixed;
	left:0;
	top:0;
	width:100%;
	z-index: 1000;
	background: cl(blue-dark);
	border-bottom: 1px solid cl(white, .21);
	height: rem(132px);
	transition: all .5s ease;
	@include media-breakpoint-down(sm) {
		height: auto;
	}
	&.small {
		height: auto;
		.header-top {
			display: none;
		}
	}
	&--inner {
		height: rem(70px)!important;
		box-shadow:rem(0 3px 3px cl(black, .06));
		padding: rem(0 23px 0 16px);
		display: flex;
		align-items: center;
		justify-content: space-between;
		gap:rem(40px);
		font-family: $fontPoppins;
		@include media-breakpoint-down(xs) {
			gap:rem(20px);
			padding: rem(0 16px);
		}
		.header-logo {
			max-width:100%;
			@include media-breakpoint-up(sm) {
				width:rem(259px)!important;
			}
			@include media-breakpoint-down(xs) {
				width:13rem!important;
			}
		}
	}
	&__left {
		flex-shrink: 0;
		@include media-breakpoint-up(xl) {
			width:rem(284px);
		}
	}
	&__side {
		display: flex;
		justify-content: space-between;
		align-items: center;
		width:100%;
		gap:rem(32px);
		@include media-breakpoint-down(xs) {
			justify-content: flex-end;
			gap:rem(20px);
		}
	}
	&__slogan {
		color:#999bac;
		font-size: rem(16px);
	}
	&__search {
		background: none;
		border:0;
		color:cl(white);
		font-size: rem(18px);
	}
	&__charts {
		width:100%;
		&.opened {
			top:100%;
			opacity: 1;
		}
		@include media-breakpoint-down(xs) {
			position: absolute;
			left:0;
			top:-100%;
			width:100%;
			background: cl(blue-dark);
			padding: 1rem;
			opacity: 0;
			transition: top .3s ease, opacity .3s ease;
		}
		.selection {
			&__txt {
				background: cl(white, .32);
				color:cl(white);
				@include placeholder(cl(white));
				&:focus {
					background: cl(white, .5);
				}
			}
			&__icon {
				color:cl(white);
			}
			&__clear {
				color:cl(white);
			}
			&__drop {
				position: absolute;
				left:0;
				top:100%;
				width:100%;
				ul {
					list-style: none;
					border-radius: rem(5px);
					background: cl(white);
					padding: rem(5px 0);
					margin: 0;
					box-shadow:rem(0 9px 35px cl(black, .14));
					max-height: rem(300px);
					overflow: auto;
					li {
						margin-bottom: 1px;
						a {
							display: block;
							color:cl(label);
							font-size:rem(16px);
							padding: rem(5px 10px 5px 30px);
							strong {
								display: inline-block;
								vertical-align: middle;
								background: #f1f4f9;
								border-radius: rem(5px);
								padding: rem(5px 15px);
								color:cl(label);
							}
							&:hover,
							&:focus {
								text-decoration: none;
								background: cl(primary);
								color:cl(white);
							}
						}
					}
				}
			}
		}
	}
	&__panel {
		display: flex;
		justify-content: flex-end;
		align-items: center;
		gap:rem(35px);
		@include media-breakpoint-down(xs) {
			gap:rem(25px);
		}
	}
	&__links {
		list-style: none;
		display: flex;
		margin: 0;
		gap:rem(35px);
		@include media-breakpoint-down(xs) {
			gap:rem(20px);
		}
		& > li {
			position: relative;
			& > a {
				font-size: rem(21px);
				color:cl(white);
				display: block;
				&:hover {
					color:cl(blue-light);
				}
			}
			.badge {
				position: absolute;
				top:rem(-10px);
				left:50%;
				background: cl(secondary);
				color:cl(white);
				font-size: rem(13px);
				line-height: rem(22px);
				height: rem(22px);
				min-width: rem(22px);
				border-radius: rem(11px);
				font-weight: normal;
				padding: rem(0 3px);
			}
			&:hover {
				a {
					color:cl(blue-light);
				}
				.note,
				.help {
					display: block;
				}
			}
		}

	}
	&-top {
		display: flex;
		justify-content: flex-end;
		align-items: center;
		padding: rem(13px 23px 0 16px);
		gap:rem(14px);
		@include media-breakpoint-down(lg) {
			padding: rem(13px 30px 0);
		}
		@include media-breakpoint-down(sm) {
			display: none;
		}
		@include media-breakpoint-down(xs) {
			//padding: rem(18px 25px 0);
			//justify-content: center;
		}
		.link {
			a {
				color:white;
				font-size: rem(15px);
				text-decoration: underline;
				&:hover {
					text-decoration: none;
				}
			}
			.new {
				background: cl(green);
				color:white;
				font-size: rem(12px);
				line-height: rem(14px);
				text-transform: uppercase;
				border-radius: rem(2px);
				padding: rem(5px);
				position: relative;
				margin: rem(0 0 0 10px);
				&:before {
					content: "";
					position: absolute;
					right:100%;
					top:rem(6px);
					width: 0;
					height: 0;
					border-top: 5px solid transparent;
					border-right: 5px solid cl(green);
					border-bottom: 5px solid transparent;
				}
			}
		}
		.sep {
			width:1px;
			height: rem(25px);
			flex-shrink: 0;
			background: cl(white, .18);
		}
	}
	&-bottom {
		display: flex;
		justify-content: space-between;
		align-items: center;
		padding: rem(16px 23px 16px 16px);
		@include media-breakpoint-down(lg) {
			padding: rem(16px 30px);
		}
		@include media-breakpoint-down(xs) {
			padding: rem(18px 25px);
		}
	}
	&-logo {
		display: block;
		width:rem(345px);
		flex-shrink: 0;
		@include media-breakpoint-down(xs) {
			width: 16rem;
		}
		img {
			max-width: 100%;
			height: auto;
		}
	}
	&-social {
		display: flex;
		gap:rem(25px);
		a {
			color:white;
			font-size: rem(16px);
			&:hover {
				color:cl(secondary);
			}
		}
	}
	&-inner {
		display: flex;
		justify-content: space-between;
		align-items: center;
		width:100%;
		@include media-breakpoint-down(lg) {
			justify-content: flex-end;
		}
	}
	&-nav {
		width:100%;
		position: relative;
		display: flex;
		justify-content: center;
		align-items: center;
		z-index: 2;
		@include media-breakpoint-down(lg) {
			position: fixed;
			right:-100%;
			top:0;
			height: 100%;
			margin: 0;
			width:30rem;
			padding: rem(80px 0 0);
			background: cl(blue-dark);
			display: flex;
			justify-content: flex-start;
			flex-direction: column;
			transition: all 0.3s linear;
			&.show {
				right:0;
				box-shadow: rem(-10px 0 20px rgba(0,0,0,0.3));
			}
		}
		@include media-breakpoint-down(sm) {
			width:100%;
		}
		&-logo {
			display: none;
			position: absolute;
			top:1rem;
			width:rem(290px);
			@extend .pos-centerX;
			@include media-breakpoint-down(lg) {
				display: block;
			}
		}
		& > ul {
			display: flex;
			gap:rem(40px);
			align-items: center;
			list-style: none;
			margin: 0;
			@include media-breakpoint-down(lg) {
				height: 100%;
				width:100%;
				overflow: auto;
				display: block;
			}
			& > li {
				position: relative;
				margin-left: rem(25px);
				@include media-breakpoint-down(lg) {
					padding: 0;
					margin: 0;
					width:100%;
					border-top:rem(1px solid cl(white, 0.1));
					&:last-child {
						border-bottom:rem(1px solid cl(white, 0.1));
					}
				}
				& > a {
					display: block;
					font-size: rem(16px);
					line-height: rem(18px);
					font-weight: 700;
					color:cl(white);
					@include media-breakpoint-down(lg) {
						display: block;
						padding: 1.5rem 2rem;
						text-align: center;
					}
					&:hover {
						text-decoration: none;
						color:cl(secondary);
					}
				}
				&.active {
					& > a {
						text-decoration: none;
						color:cl(secondary);
					}
				}
				&:hover {
					.drop {
						display: block;
					}
				}
				.drop {
					position: absolute;
					top:rem(17px);
					padding-top: rem(21px);
					left:0;
					width:rem(250px);
					display: none;
					@include media-breakpoint-down(lg) {
						display: block;
						position: static;
						width:100%;
						padding: 0;
						margin: 0;
					}
					ul {
						margin: 0;
						background: cl(secondary);
						padding: rem(2px);
						list-style: none;
						box-shadow:rem(1px 1px 6px cl(black, .21));
						@include media-breakpoint-down(lg) {
							box-shadow: none;
							padding: 0;
							background: none;
						}
						li {
							margin-top: rem(1px);
							&:first-child {
								margin-top: 0;
							}
							a {
								display: block;
								font-size: rem(15px);
								line-height: rem(18px);
								color:cl(white);
								font-weight: 600;
								padding: rem(10px 10px 10px 20px);
								@include media-breakpoint-down(lg) {
									display: block;
									padding: 1.5rem 2rem;
									text-align: center;
									border-top:rem(1px solid cl(white, .1));
								}
								&:hover {
									text-decoration: none;
									color:cl(white);
									background: cl(blue-dark);
								}
							}
							&.active {
								a {
									color:cl(white);
									background: cl(blue-dark);
									@include media-breakpoint-down(lg) {
										color:cl(secondary);
										background: none;
									}
								}
							}
						}
					}
				}
			}
		}
	}
	&-buttons {
		position: relative;
		display: flex;
		align-items: center;
		gap:rem(16px);
		@include media-breakpoint-down(xs) {
			//margin: 0;
			//padding: 1rem;
			justify-content: space-between;
			width:100%;
			padding: 1rem;
		}
		.link {
			color:cl(white);
			font-size: rem(16px);
			font-weight: 700;
			letter-spacing: rem(.4px);
			&:focus,
			&:hover {
				outline: none;
				text-decoration: none;
				color:cl(primary);
			}
			@include media-breakpoint-down(xs) {
				background: none;
				border:1px solid cl(white);
				text-align: center;
				width:48%;
				line-height: rem(42px);
				color:cl(white);
				border-radius: rem(5px);
			}
		}
		.btn {
			@include media-breakpoint-down(xs) {
				//width:48%;
				//margin: 0;
			}
		}
	}
	.user {
		display: flex;
		justify-content: flex-end;
		align-items: center;
		list-style: none;
		position: relative;
		gap:rem(6px);
		@include media-breakpoint-down(xs) {
		}
		&:hover {
			.user__drop {
				display: block;
			}
		}
		&__photo {
			width:rem(40px);
			height:rem(40px);
			border-radius: 50%;
			flex-shrink: 0;
			background-size: cover;
			background-repeat: no-repeat;
			background-position: center;
			display: flex;
			justify-content: center;
			align-items: center;
			&--lg {
				width:rem(80px);
				height:rem(80px);
			}
			@include media-breakpoint-down(xs) {
				width:rem(50px);
				height:rem(50px);
			}
		}
		&__arrow {
			font-size:rem(20px);
			color:cl(white);
		}
		&__drop {
			position: absolute;
			right:0;
			width:rem(380px);
			top:rem(35px);
			padding:rem(20px 0 0 0);
			display: none;
		}
		&__inner {
			background: cl(white);
			border-radius: rem(10px);
			overflow: hidden;
			box-shadow:0 9px 35px rgba(0,0,0,0.14);
		}
		&__profile {
			display: flex;
			align-items: center;
			gap:rem(14px);
			padding: rem(20px 23px);
		}
		&__name {
			strong {
				display: block;
				margin: rem(0 0 15px);
				color:cl(title);
				font-size:rem(16px);
				line-height:rem(16px);
			}
			.btn {
				font-weight: normal;
				font-size:rem(16px);
				line-height: rem(38px);
				width:rem(150px);
				background: cl(blue-light);
				border-color:cl(blue-light);
				&:hover {
					background: cl(blue-light, .9);
				}
			}
		}
		&__list {
			list-style: none;
			margin: 0;
			border-top:1px solid #d6dae0;
			border-bottom:1px solid #d6dae0;
			li {
				font-size:rem(16px);
				&:not(:first-child) {
					margin-top: 1px;
				}
				a {
					color:cl(text);
					display: flex;
					gap:rem(22px);
					align-items: center;
					height: rem(46px);
					padding:rem(0 20px);
					white-space: nowrap;
					&:hover,
					&.active {
						text-decoration: none;
						color: cl(white);
						background: cl(primary);
					}
					i {margin: 0 0.5rem 0 0;}
				}
			}
		}
		&__icon {
			flex-shrink: 0;
			width:rem(27px);
			svg {
				display: block;
				max-width:100%;
				height: auto;
				fill:currentColor;
			}
		}
		&__banner {
			display: flex;
			gap:rem(22px);
			padding: rem(20px 23px);
			color: cl(white);
			font-size:rem(18px);
			line-height: calc(26 / 18);
			background: radial-gradient(ellipse at bottom right, #394b8b 0%, cl(blue-dark) 100%);

			.user__icon {
				color:cl(green);
			}
		}
	}
	.note {
		position: absolute;
		right:rem(-20px);
		width:rem(380px);
		top:rem(20px);
		padding:rem(27px 0 0 0);
		display: none;
		@include media-breakpoint-down(xs) {
			right:rem(-50px);
		}
		&__drop {
			background: cl(white);
			border-radius: rem(10px);
			overflow: hidden;
			box-shadow:0 9px 35px rgba(0,0,0,0.14);
		}
		&__ttl {
			display: flex;
			justify-content: space-between;
			align-items: center;
			border-bottom:1px solid cl(back);
			padding: rem(15px 20px);
			strong {
				font-size: rem(18px);
				font-weight: 600;
				color:cl(title);
			}
			a {
				font-size: rem(15px);
			}
		}
		.menu-scroll {
			height: rem(210px);
		}
		&__list {
			list-style: none;
			li {
				font-size: rem(15px);
				position: relative;
				padding: rem(15px 20px 15px 40px);
				&:not(:last-child) {
					border-bottom: 1px solid cl(back);
				}
				&.new {
					&:before {
						content: "";
						position: absolute;
						left:rem(20px);
						top:rem(18px);
						width:rem(8px);
						height: rem(8px);
						background: cl(green);
						border-radius: 50%;
					}
				}
			}
		}
		&__all {
			border-top:1px solid cl(back);
			padding: rem(15px 20px);
			font-size: rem(15px);
			text-align: center;
			background: #f9f9f9;
		}
	}
	.help {
		position: absolute;
		right:rem(-20px);
		width:rem(300px);
		top:rem(20px);
		padding:rem(27px 0 0 0);
		display: none;
		&__drop {
			background: cl(white);
			border-radius: rem(10px);
			overflow: hidden;
			box-shadow:0 9px 35px rgba(0,0,0,0.14);
		}
		&__ttl {
			display: flex;
			justify-content: space-between;
			align-items: center;
			border-bottom:1px solid cl(back);
			padding: rem(15px 20px);
			strong {
				font-size: rem(18px);
				font-weight: 600;
				color:cl(title);
			}
		}
		&__list {
			list-style: none;
			li {
				font-size: rem(15px);
				a {
					display: flex;
					align-items: center;
					gap:rem(10px);
					color:cl(text);
					padding: rem(15px 20px);
					&:hover {
						background: cl(primary);
						color:cl(white);
						text-decoration: none;
					}
					i {
						flex-shrink: 0;
					}
				}
				&.active {
					a {
						background: cl(primary);
						color:cl(white);
					}
				}
			}
		}
	}
}

/*hamburger btn*/
.btn-lines {
	position: absolute;
	display: block;
	left: 0;
	width: 100%;
	height:rem(2px);
	background-color: cl(white);
	content: "";
	transition: all 0.3s ease;
}
.c-hamburger {
	display: none;
	position: relative;
	//margin: rem(0 0 0 40px);
	padding: 0;
	height:rem(20px);
	width:rem(26px);
	flex-shrink: 0;
	font-size: 0;
	text-indent: -9999px;
	appearance: none;
	border: none;
	cursor: pointer;
	transition: all 0.3s ease  0.3s;
	flex-shrink: 0;
	&:focus {
		outline: none;
	}
	@include media-breakpoint-down(lg){
		display: block;
	}
	@include media-breakpoint-down(xs){
		margin: 0;
	}
	span {
		display: block;
		position: absolute;
		top:rem(9px);
		left: 0;
		right: 0;
		height:rem(2px);
		background: cl(white);
		transition: all 0.3s ease;
	}
	span::before {
		@extend .btn-lines;
		top:rem(-7px);
	}
	span::after {
		@extend .btn-lines;
		bottom:rem(-7px);
	}
	&--htx {
		background-color: transparent;
		&.active {
			position: absolute;
			right:2rem;
			top:1.7rem;
			@include media-breakpoint-down(xs) {
				right:1.5rem;
			}
			&:before {
				display: none;
			}
			span {
				background: none;
			}
			span::before {
				top: 0;
				transform:rotate(45deg);
				//background: cl(white);
			}
			span::after {
				bottom: 0;
				transform:rotate(-45deg);
				//background: cl(white);
			}
		}
	}
}
/*hamburger btn*/
