.home {
  h2 {
    text-align: center;
    font-size: rem(40px);
    font-weight: 700;
    margin: rem(0 0 30px);
    color:cl(title);
    @include media-breakpoint-down(xs) {
      font-size: 1.8rem;
    }
  }
  &-videos {
    background: white url(/img/line-shadow.png) no-repeat 100% 0;
    position: relative;
    z-index: 10;
    padding: rem(60px 0);
    @include media-breakpoint-down(xs) {
      padding: 3.2rem 0;
    }
    &-grid {
      display: grid;
      grid-template-columns: repeat(2, 1fr);
      grid-gap: rem(45px 54px);
      padding: rem(0 0 40px);
      @include media-breakpoint-down(xs) {
        grid-template-columns: repeat(1, 1fr);
      }
    }
    &-item {
      background: #edf2f9;
      border-radius: rem(6px);
      height: rem(165px);
      display: flex;
      justify-content: space-between;
      align-items: flex-start;
      flex-direction: row-reverse;
      position: relative;
      @include media-breakpoint-down(md) {
        flex-direction: column;
        height: auto;
      }
      h3 {
        font-size: rem(18px);
        line-height: rem(26px);
        font-weight: 700;
        margin: 0;
        height: rem(78px);
        overflow: hidden;
        text-overflow: ellipsis;
        display: -webkit-box;
        -webkit-line-clamp: 3;
        -webkit-box-orient: vertical;
        color:cl(title);
      }
      .btn {
        position: absolute;
        left:rem(23px);
        bottom: rem(-14px);
        line-height: rem(30px);
        font-size: rem(13px);
        padding: rem(0 15px);
        border-radius: rem(3px);
        text-transform: uppercase;
        font-weight: normal;
        @include gradient-x(#2a60c4, #1342a4);
        box-shadow: rem(0 5px 10px cl(black, .18));
        img {
          width:rem(21px);
          height: auto;
          vertical-align: middle;
        }
      }
    }
    &-inner {
      position: relative;
      height: 100%;
      display: flex;
      justify-content: flex-start;
      align-items: center;
      padding: rem(0 23px 23px 23px);
    }
    &-play {
      position: absolute;
      top: 5rem;
      left: 50%;
      transform: translateX(-50%);
      width:rem(70px);
      height: rem(70px);
      border-radius: 50%;
      padding: rem(7px);
      background: cl(white, .3);
      //margin: rem(-30px 0 0 60px);
      box-shadow:rem(0 19px 14px cl(black, .16));
      @include media-breakpoint-up(lg) {
        position: absolute;
        top: 50%;
        left: 50%;
        transform: translate(-50%,-50%);
      }
      z-index: 100;
      a {
        width:100%;
        height: 100%;
        border-radius: 50%;
        background: cl(white);
        display: flex;
        justify-content: center;
        align-items: center;
        color:#5f85e1;
        font-size: rem(18px);
        padding: rem(0 0 0 3px);
        transition: all .3s linear;
        &:hover {
          text-decoration: none;
          background: #5f85e1;
          color:white;
        }
      }
    }
    &-image {
      flex-shrink: 0;
      height: 14rem;
      width:100%;
      position: relative;
      @include media-breakpoint-down(md) {
        border-radius: rem(6px 6px 0 0);
        overflow: hidden;
      }
      @include media-breakpoint-up(lg) {
        height: rem(180px);
        width:rem(318px);
        &:before {
          content: "";
          position: absolute;
          right:0;
          top:0;
          height: 100%;
          width:83%;
          border-radius: rem(6px);
          box-shadow: rem(0 4px 35px cl(black, .16));
        }
      }
      @include media-breakpoint-down(md) {
        margin-bottom: 1.5rem;
      }
      &-inner {
        width:100%;
        height: 100%;
        position: relative;
        @include media-breakpoint-up(lg) {
          -webkit-mask: url(../../../../assets/images/wave/video-image-mask.png);
          -webkit-mask-size: 100%; /* change it */
          -webkit-mask-position: center;
          -webkit-mask-repeat: no-repeat;
        }
        div {
          position: absolute;
          left:0;
          top:0;
          width:100%;
          height: 100%;
          background-repeat: no-repeat;
          background-size: cover;
          background-position: 50% 50%;
        }
      }
    }
  }
  &-instruments {
    position: relative;
    overflow: hidden;
    background: cl(blue-dark);
    padding: rem(65px 0);
    color:white;
    font-size: rem(16px);
    line-height: rem(26px);
    @include media-breakpoint-down(xs) {
      padding: 3rem 0;
    }
    .container {
      position: relative;
      z-index: 2;
    }
    h2 {
      text-align: left;
      margin: rem(0 0 15px);
      color:white;
    }
    .nav {
      &-tabs {
        flex-direction: column;
        border:0;
        margin: 0 0 2rem;
      }
      &-item {
        margin: 0 0 1px;
      }
      &-link {
        padding: rem(12px 25px);
        display: flex;
        align-items: center;
        gap:rem(23px);
        border-radius: rem(10px);
        border:0;
        color:white;
        font-size: rem(24px);
        font-weight: 700;
        @include media-breakpoint-down(sm) {
          gap:rem(15px);
          font-size: rem(18px);
          padding: rem(8px 20px);
        }
        img {
          width:rem(55px);
          height: auto;
          @include media-breakpoint-down(sm) {
            width:rem(40px);
          }
        }
        &.active {
          background: cl(white, .1);
          color:white;
        }
        &:hover {
          @include media-breakpoint-up(xl) {
            background: cl(white, .1);
          }
        }
      }
    }
    .tab {
      &-content {
        margin: rem(0 0 40px);
        @include media-breakpoint-up(md) {
          border-radius: rem(10px);
          background: cl(white, .1);
          padding: rem(25px 50px);
        }
        h3 {
          font-size: rem(24px);
          text-align: center;
          font-weight: 700;
          margin: rem(0 0 5px);
          @include media-breakpoint-down(sm) {
            display: none;
          }
        }
        .chart {
          display: block;
          margin: rem(0 auto 20px);
          max-width:100%;
        }
        .text-scroll-light {
          height: rem(140px);
          @include media-breakpoint-down(sm) {
            overflow: auto;
          }
        }
        .list {
          list-style: none;
          margin: 0;
          display: grid;
          grid-template-columns: repeat(4, 1fr);
          grid-gap: rem(10px);
          &--three_col {
            grid-template-columns: repeat(3, 1fr);
          }
          @include media-breakpoint-down(xs) {
            grid-template-columns: repeat(2, 1fr);
          }
          li {
            display: flex;
            gap:rem(7px);
            align-items: center;
            color:#9c9db1;
            font-size: rem(15px);
            line-height: rem(18px);
            //text-transform: uppercase;
            .flags {
              width:rem(23px);
              height: rem(20px);
              display: flex;
              justify-content: center;
              align-items: center;
              position: relative;
              img {
                width:rem(16px);
                height: auto;
                &:nth-last-child(2) {
                  position: absolute;
                  top:0;
                  left:0;
                }
                &:nth-child(2) {
                  position: absolute;
                  bottom: 0;
                  right:0;
                }
              }
            }
          }
        }
      }
    }
    @include media-breakpoint-up(md) {
      .panel-heading{
        display:none;
      }
      .panel{
        border:none;
        box-shadow:none;
      }
      .panel-collapse{
        height:auto;
        &.collapse{
          display:block;
        }
      }
    }
    @include media-breakpoint-down(sm) {
      .fade:not(.show) {
        opacity: 1;
      }
      .tab-pane{
        display:block;
      }
      .nav-tabs{
        display:none;
      }
      .panel-title a{
        display:block;
      }
      .panel{
        border-radius: rem(10px);
        background: cl(white, .1);
        margin: rem(0 0 10px);
      }
      .panel-collapse {
        padding: rem(20px 25px);
      }
    }
    &-shapes {
      &-base {
        &--one {
          left:40%;
          top:rem(-50px);
        }
        &--two {
          bottom:rem(-80px);
          left:rem(-100px);
        }
        &--three {
          bottom:rem(-80px);
          right:rem(-100px);
        }
      }
    }
    .decor {
      left:rem(110px);
      top: rem(45px);
    }
    .circles {
      right:10%;
      bottom: 10%;
    }
  }
  &-chart {
    background: white url(/img/line-shadow-2.png) no-repeat 0 100%;
    position: relative;
    z-index: 10;
    padding: rem(80px 0);
    overflow: hidden;
    @include media-breakpoint-down(xs) {
      padding: 3.5rem 0;
    }
    .container {
      position: relative;
      z-index: 2;
    }
    h2 {
      margin: rem(0 0 50px);
    }
    h3 {
      font-size: rem(30px);
      line-height: rem(40px);
      font-weight: normal;
      margin: rem(0 0 25px);
      color:cl(title);
    }
    &-image {
      display: block;
      overflow: hidden;
      border-radius: rem(10px);
      box-shadow: rem(0 9px 35px cl(black, .14));
      margin: rem(0 35px 30px -65px);
      @include media-breakpoint-down(lg) {
        margin-left: 0;
      }
      @include media-breakpoint-down(md) {
        margin-right: 0;
      }
      img {
        max-width:100%;
        height: auto;
      }
    }
    &-text {
      font-size: rem(16px);
      line-height: rem(26px);
      letter-spacing: rem(.2px);
    }
    .decor {
      top:rem(140px);
      left:rem(220px);
      @include media-breakpoint-down(lg) {
        top:2rem;
        left:2rem;
      }
    }
  }
  &-box {
    position: relative;
    background: cl(blue-dark);
    padding: rem(65px 0);
    color:white;
    font-size: rem(16px);
    line-height: rem(26px);
    z-index: 11;
    @include media-breakpoint-down(xs) {
      padding-top: 3rem;
    }
    .container {
      position: relative;
      z-index: 2;
    }
    h2 {
      color:white;
      margin: rem(0 0 24px);
    }
    &-image {
      margin: rem(-95px 0 -160px);
      position: relative;
      -webkit-transform: translate3d(0px, 0px, 0px);
      @include media-breakpoint-down(lg) {
        margin-top: -4rem;
      }
      @include media-breakpoint-down(xs) {
        margin: -1rem 0 -5rem;
      }
      &-hold {
        position: relative;
        z-index: 2;
        -webkit-transform: translate3d(0px, 0px, 0px);
      }
      &-screen {
        max-width:88%;
        display: block;
        margin: 0 auto;
        box-shadow: rem(0 20px 32px cl(black, .14));
        border-radius: rem(25px);
        transform: perspective(2000px) rotateX(45deg);
        @include media-breakpoint-down(xs) {
          max-width: 82%;
          transform: perspective(250px) rotateX(30deg);
        }
      }
      &-text {
        position: absolute;
        font-family: $fontBradleyHandBold;
        font-size: rem(18px);
        line-height: rem(25px);
        letter-spacing: rem(.2px);
        padding: rem(0 0 10px);
        width:rem(225px);
        color:#5f85e1;
        z-index: 3;
        -webkit-transform: translate3d(0px, 0px, 0px);
        @include media-breakpoint-down(lg) {
          width:rem(200px);
        }
        p {
          margin: 0;
        }
        &--left {
          right: 93%;
          top: 22%;
          text-align: right;
          img {
            right:rem(-78px);
          }
        }
        &--right {
          bottom: 54%;
          left: 95%;
          img {
            left:rem(-51px);
          }
        }
        img {
          width:rem(122px);
          height: auto;
          position: absolute;
          top:100%;
        }
      }
    }
    &-play {
      -webkit-transform: translate3d(0px, 0px, 0px);
      position: absolute;
      top:0;
      left:0;
      width:100%;
      height: 100%;
      display: flex;
      justify-content: center;
      align-items: center;
      z-index: 100;
      &-inner {
        width:rem(110px);
        height: rem(110px);
        border-radius: 50%;
        padding: rem(12px);
        background: cl(white, .3);
        box-shadow:rem(0 19px 14px cl(black, .16));
        @include media-breakpoint-down(xs) {
          width:rem(90px);
          height: rem(90px);
          padding: rem(10px);
        }
      }
      a {
        width:100%;
        height: 100%;
        border-radius: 50%;
        @include gradient-x($gradientFirst, $gradientSecond);
        display: flex;
        justify-content: center;
        align-items: center;
        color:white;
        font-size: rem(22px);
        padding: rem(0 0 0 3px);
        transition: all .3s linear;
        &:hover {
          text-decoration: none;
          color:white;
          @include gradient-x($gradientSecond, $gradientFirst);
        }
        @include media-breakpoint-down(xs) {
          font-size: rem(20px);
        }
      }
    }
    .shapes {
      &-base {
        &--one {
          left:30%;
          top:rem(-50px);
        }
        &--two {
          bottom:auto;
          top:rem(-110px);
          left:rem(-80px);
        }
        &--three {
          bottom:rem(-80px);
          right:rem(-100px);
        }
      }
    }
    .decor {
      left: 7%;
      bottom: 10%;
      @include media-breakpoint-down(xs) {
        bottom: auto;
        top:6%;
      }
    }
    .circles {
      right:5%;
      top: 10%;
    }
  }
  &-articles {
    padding: rem(135px 0 110px);
    background: white url(/img/line-shadow.png) no-repeat 100% 0;
    position: relative;
    z-index: 10;
    @include media-breakpoint-down(xs) {
      padding: 5.4375rem 0 2.875rem;
    }
    &-prev,
    &-next {
      @extend .pos-centerY;
      left:rem(-60px);
      font-size: rem(18px);
      width:rem(46px);
      height: rem(46px);
      border-radius: 50%;
      border:1px solid #b2b5b8;
      background: white;
      color:#b2b5b8;
      display: flex;
      justify-content: center;
      align-items: center;
      &:hover {
        text-decoration: none;
        background: #b2b5b8;
        color:white;
      }
      @include media-breakpoint-down(lg) {
        width:rem(30px);
        height: rem(30px);
        font-size: rem(16px);
        left:rem(-16px);
      }
      @include media-breakpoint-down(xs) {
        left:rem(-10px);
      }
    }
    &-next {
      left: auto;
      right:rem(-60px);
      @include media-breakpoint-down(lg) {
        right:rem(-16px);
      }
      @include media-breakpoint-down(xs) {
        right:rem(-10px);
      }
    }
    &-item {
      margin: rem(5px 15px 25px);
      padding: rem(0 25px 16px);
      border-radius: rem(10px);
      overflow: hidden;
      box-shadow: rem(0 10px 20px cl(black, .14));
      background: white;
      @include media-breakpoint-down(xs) {
        margin: rem(5px 28px 25px);
      }
    }
    &-title {
      margin: rem(0 -25px 16px);
      background: white url(/img/home-articles-title.png) no-repeat 50% 50%;
      background-size: cover;
      color:white;
      height: rem(164px);
      padding: rem(23px 25px 5px);
      &-mark {
        display: inline-block;
        background: cl(green);
        text-transform: uppercase;
        font-weight: 700;
        font-size: rem(14px);
        line-height: rem(28px);
        padding: rem(0 10px);
        border-radius: rem(3px);
        margin: rem(0 0 15px);
      }
      h3 {
        text-transform: uppercase;
        font-size: rem(24px);
        line-height: rem(30px);
        font-weight: 700;
        margin: 0;
        height: rem(90px);
        overflow: hidden;
        text-overflow: ellipsis;
        display: -webkit-box;
        -webkit-line-clamp: 3;
        -webkit-box-orient: vertical;
        color:cl(white);
      }
    }
    &-date {
      font-size: rem(14px);
      margin: rem(0 0 15px);
    }
    h4 {
      font-size: rem(18px);
      line-height: rem(26px);
      font-weight: 700;
      margin: rem(0 0 15px);
      height: rem(52px);
      overflow: hidden;
      text-overflow: ellipsis;
      display: -webkit-box;
      -webkit-line-clamp: 2;
      -webkit-box-orient: vertical;
      color:cl(title);
      a {
        color:cl(title);
        &:hover {
          text-decoration: none;
          color:cl(primary);
        }
      }
    }
    &-text {
      font-size: rem(15px);
      line-height: rem(24px);
      height: rem(48px);
      overflow: hidden;
      text-overflow: ellipsis;
      display: -webkit-box;
      -webkit-line-clamp: 2;
      -webkit-box-orient: vertical;
      margin: rem(0 0 20px);
    }
    &-bottom {
      display: flex;
      justify-content: space-between;
      align-items: center;
    }
    &-author {
      display: flex;
      justify-content: flex-start;
      align-items: center;
      gap:rem(12px);
      &-photo {
        flex-shrink: 0;
        width:rem(42px);
        height: rem(42px);
        border-radius: 50%;
        background-size: cover;
        background-repeat: no-repeat;
        background-position: 50% 50%;
      }
      &-name {
        font-size: rem(14px);
        a {
          color:cl(text);
        }
      }
    }
    &-more {
      font-size: rem(18px);
      width:rem(40px);
      height: rem(40px);
      border-radius: 50%;
      display: flex;
      justify-content: center;
      align-items: center;
      &:hover {
        text-decoration: none;
        background: cl(primary);
        color:white;
      }
    }
  }
}