.decor {
  position: absolute;
  width:rem(120px);
  span {
    position: absolute;
    width:rem(23px);
    height: rem(23px);
    border-radius: 50%;
    border:1px solid cl(green);
    &.red {
      border:1px solid #e35143;
    }
    &.top_right {
      top:rem(26px);
      right:rem(-6px);
      animation: circleTrack 5s linear infinite alternate;
    }
    &.bottom_right {
      bottom:rem(20px);
      right:rem(1px);
      animation: circleTrackDown 5s linear infinite alternate;
    }
    &.top_left {
      top:0;
      left:rem(16px);
      animation: circleTrackLeft 5s linear infinite alternate;
    }
  }
  img {
    width:100%;
    height: auto;
    opacity: .15;
  }
}

.shapes {
  position: absolute;
  top:0;
  left:0;
  width:100%;
  height: 100%;
  z-index: 1;
  overflow: hidden;
  &-base {
    position: absolute;
    filter: blur(60px);
    opacity: 0.8;
    &--one {
      border-radius: 100%;
      width: rem(300px);
      height: rem(300px);
      background-color: #212d6f;
      left:40%;
      top:rem(-50px);
      z-index: 3;
      animation: fly 12s linear infinite alternate;
      transform:rotate(0) translate(80px) rotate(0);
    }
    &--two {
      border-radius: 100%;
      width: rem(300px);
      height: rem(600px);
      bottom:rem(-80px);
      left:rem(-100px);
      background-color: #1c2763;
      animation: flyPlus 8s linear infinite alternate;
      transform:rotate(0) translate(100px) rotate(0);
    }
    &--three {
      border-radius: 100%;
      width: rem(700px);
      height: rem(350px);
      bottom:rem(-80px);
      right:rem(-100px);
      background-color: #243272;
      animation: flyPlus 8s linear infinite;
      transform:rotate(0) translate(100px) rotate(0);
    }
  }
}

.circles {
  position: absolute;
  width:rem(200px);
  height:rem(200px);
  &-item {
    @extend .pos-center;
    width: var(--size);
    height: var(--size);
    &--1 {
      --size: 25px;
      animation-delay: 1s;
    }
    &--2 {
      --size: 50px;
    }
    &--3 {
      --size: 75px;
    }
    &--4 {
      --size: 100px;
    }
    &--5 {
      --size: 125px;
    }
    &--6 {
      --size: 150px;
    }
    &--7 {
      --size: 175px;
    }
    &--8 {
      --size: 200px;
    }
    div {
      width:100%;
      height: 100%;
      border:1px solid cl(white, .21);
      border-radius: 50%;
    }
  }
  span {
    position: absolute;
    width:rem(23px);
    height: rem(23px);
    border-radius: 50%;
    border:1px solid cl(green);
    &.blue {
      border:1px solid #5f85e1;
    }
    &.red {
      border:1px solid #e35143;
    }
    &.top_right {
      top:rem(30px);
      right:rem(2px);
      animation: circleTrack 5s linear infinite alternate;
    }
    &.bottom_right {
      bottom:rem(20px);
      right:rem(1px);
      animation: circleTrackDown 5s linear infinite alternate;
    }
    &.bottom_left {
      bottom:rem(36px);
      left:rem(4px);
      animation: circleTrackUp 5s linear infinite alternate;
    }
  }
}
@keyframes circleTrackUp {
  0% {
    transform:translate(0);
  }
  50% {
    transform:translate(-12px, -26px);
  }
  100% {
    transform:translate(-16px, -54px);
  }
}
@keyframes circleTrackLeft {
  0% {
    transform:translate(0);
  }
  50% {
    transform:translate(-18px, 23px);
  }
  100% {
    transform:translate(-26px, 46px);
  }
}
@-webkit-keyframes circleTrackLeft {
  0% {
    transform:translate(0);
  }
  50% {
    transform:translate(-18px, 23px);
  }
  100% {
    transform:translate(-26px, 46px);
  }
}
@keyframes circleTrackDown {
  0% {
    transform:translate(0);
  }
  50% {
    transform:translate(-23px, 13px);
  }
  100% {
    transform:translate(-46px, 26px);
  }
}
@-webkit-keyframes circleTrackDown {
  0% {
    transform:translate(0);
  }
  50% {
    transform:translate(-23px, 13px);
  }
  100% {
    transform:translate(-46px, 26px);
  }
}
@keyframes circleTrack {
  0% {
    transform:translate(0);
  }
  50% {
    transform:translate(-20px, -20px);
  }
  100% {
    transform:translate(-40px, -35px);
  }
}
@-webkit-keyframes circleTrack {
  0% {
    transform:translate(0);
  }
  50% {
    transform:translate(-20px, -20px);
  }
  100% {
    transform:translate(-40px, -35px);
  }
}
@keyframes fly {
  100% {
    transform:rotate(1turn) translate(100px) rotate(-1turn);
  }
}
@-webkit-keyframes fly {
  100% {
    transform:rotate(1turn) translate(100px) rotate(-1turn);
  }
}
@keyframes flyPlus {
  100% {
    transform:rotate(-1turn) translate(100px) rotate(1turn);
  }
}
@-webkit-keyframes flyPlus {
  100% {
    transform:rotate(-1turn) translate(100px) rotate(1turn);
  }
}