.education {
  &-main {
    padding: rem(70px 0 80px);
    position: relative;
    background: #f3f6fb url(/img/line-shadow-6.png) no-repeat 0 0;
    overflow: hidden;
    font-size: rem(16px);
    line-height: rem(26px);
    .decor {
      bottom: 10%;
      right: rem(80px);
      @include media-breakpoint-down(md) {
        right: rem(30px);
        bottom: rem(30px);
      }
    }
  }
  &-grid {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    grid-gap:rem(48px 30px);
    @include media-breakpoint-down(md) {
      grid-template-columns: repeat(2, 1fr);
    }
    @include media-breakpoint-down(xs) {
      grid-gap:rem(30px 16px);
    }
  }
  &-item {
    min-height: rem(230px);
    background: white;
    border:rem(1px solid white);
    border-radius: rem(10px);
    padding: rem(40px 30px 20px);
    box-shadow:rem(0 6px 13px cl(black, .08));
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    align-items: center;
    text-align: center;
    color:cl(title);
    position: relative;
    &:hover {
      text-decoration: none;
      border-color:cl(primary);
      box-shadow:rem(0 6px 13px rgba(124, 155, 231, .53));
    }
    @include media-breakpoint-down(xs) {
      padding: rem(30px 15px 20px);
      min-height: auto;
    }
    .education-discount {
      left:rem(-1px);
    }
    img {
      height: rem(70px);
      width:auto;
      margin: rem(0 0 25px);
      @include media-breakpoint-down(xs) {
        height: rem(60px);
        margin: rem(0 0 15px);
      }
    }
    h3 {
      font-size: rem(18px);
      line-height: rem(26px);
      color:cl(title);
      font-weight: 700;
      margin: 0;
      @include media-breakpoint-down(xs) {
        font-size: rem(16px);
        line-height: rem(22px);
      }
    }
  }
  &-section {
    padding: rem(70px 0 80px);
    position: relative;
    background: #fff url(/img/line-shadow-5.png) no-repeat 0 0;
    background-size: rem(830px auto);
    color:#4b5565;
    font-size: rem(18px);
    line-height: rem(28px);
    h2 {
      font-size: rem(40px);
      margin: rem(0 0 30px);
      font-weight: 700;
      color:cl(title);
    }
    ul {
      list-style: none;
      li {
        position: relative;
        padding: rem(0 0 0 40px);
        margin-bottom: rem(10px);
        background: url(/img/list-mark.png) no-repeat 0 .313rem;
        background-size: rem(17px 17px);
      }
    }
    .decor {
      right: 3%;
      bottom: 25%;
    }
  }
  &-discount {
    position: absolute;
    left:0;
    top:rem(-21px);
    background: cl(secondary);
    color:white;
    padding: rem(0 9px 0 16px);
    font-size: rem(16px);
    line-height: rem(34px);
    font-weight: 700;
    border-radius: rem(17px 0 0 0);
    z-index: 3;
    &:before {
      content:"";
      position: absolute;
      top:0;
      left:100%;
      width: 0;
      height: 0;
      border-top: rem(17px solid transparent);
      border-left: rem(12px solid cl(secondary));
      border-bottom: rem(17px solid transparent);
    }
  }
  &-plan {
    background: white;
    border-radius: rem(10px);
    box-shadow:rem(0 9px 35px cl(black, .14));
    position: relative;
    z-index: 10;
    font-size: rem(16px);
    line-height: rem(26px);
    margin: rem(30px 0 0);
    @include media-breakpoint-up(lg) {
      margin: rem(-130px 0 0);
    }
    &-video {
      padding-top: 56.25%;
      background-size: cover;
      background-position: 50% 50%;
      background-repeat: no-repeat;
      position: relative;
      border-radius: rem(10px 10px 0 0);
      overflow: hidden;
      &:before {
        content: "";
        @extend .full;
        background: cl(black, .13);
        z-index: 1;
      }
      &-play {
        @extend .pos-center;
        border:rem(8px solid cl(white,.5));
        width:rem(70px);
        height: rem(70px);
        font-size: rem(15px);
        border-radius: 50%;
        z-index: 2;
        span {
          width:100%;
          height: 100%;
          background: white;
          border-radius: 50%;
          display: flex;
          justify-content: center;
          align-items: center;
          padding: rem(0 0 0 3px);
        }
        &:hover {
          text-decoration: none;
          color:white;
          span {
            background: cl(primary);
          }
        }
      }
    }
    &-inner {
      padding: rem(25px 23px 25px);

    }
    &-price {
      font-size: rem(34px);
      margin: rem(0 0 15px);
      color:#4b5565;
      span {
        text-decoration: line-through;
        color:#9c9db1;
        font-size: rem(18px);
      }
    }
    &-available {
      color:cl(secondary);
      font-size: rem(14px);
      margin: rem(0 0 20px);
    }
    &-rating {
      color:#9a9a9a;
      font-size: rem(14px);
      i {
        font-size: rem(16px);
      }
    }
    p {
      margin: 0;
    }
  }
  &-block {
    padding: rem(55px 0 80px);
    position: relative;
    background: #fff url(/img/line-shadow.png) no-repeat 100% 0;
    &--no-image {
      background: #fff;
    }
    h2 {
      font-size: rem(40px);
      margin: rem(0 0 30px);
      font-weight: 700;
      color:cl(title);
      @include media-breakpoint-down(xs) {
        font-size: rem(30px);
      }
    }
    .decor {
      right: 3%;
      bottom: 25%;
    }
  }
  &-all {
    display: inline-block;
    vertical-align: top;
    font-weight: 600;
    font-size: rem(15px);
    line-height: rem(25px);
    letter-spacing: rem(.2px);
  }
  &-slider {
    margin: rem(40px 0 45px);
    z-index: 2;
    @include media-breakpoint-down(lg) {
      padding: rem(0 60px);
    }
    @include media-breakpoint-down(xs) {
      padding: 0 1.3rem;
    }
    &-prev,
    &-next {
      position: absolute;
      width:rem(46px);
      height: rem(46px);
      border-radius: 50%;
      border:1px solid #b2b5b8;
      background: none;
      color:#b2b5b8;
      left:rem(-62px);
      top:rem(110px);
      @include media-breakpoint-down(lg) {
        left:0;
      }
      @include media-breakpoint-down(xs) {
        width:rem(32px);
        height: rem(32px);
        font-size: rem(14px);
        left:-.5rem;
      }
    }
    &-next {
      left:auto;
      right:rem(-62px);
      @include media-breakpoint-down(lg) {
        right:0;
      }
      @include media-breakpoint-down(xs) {
        right:-.5rem;
      }
    }
    &-item {
      display: block;
      position: relative;
      margin: rem(20px 13px 20px);
      box-shadow:rem(0 6px 13px cl(black, .08));
      background: white;
      border-radius: rem(10px);
      &:hover {
        text-decoration: none;
        box-shadow:rem(0 6px 13px rgba(124, 155, 231, .53));
        .education-slider-image-img {
          transform: scale(1.1);
        }
      }
    }
    &-image {
      display: block;
      position: relative;
      border-radius: rem(10px 10px 0 0);
      height: rem(142px);
      overflow: hidden;
      padding: rem(30px 18px 10px);
      background: #092d77;
      &-img {
        @extend .full;
        background-size: cover;
        background-repeat: no-repeat;
        background-position: 50% 50%;
        transition: all .3s ease;
      }
    }
    &-ttl {
      display: block;
      color:white;
      text-transform: uppercase;
      font-size: rem(22px);
      line-height: rem(24px);
      position: relative;
      z-index: 2;
      height: rem(96px);
      overflow: hidden;
      text-overflow: ellipsis;
      display: -webkit-box;
      -webkit-line-clamp: 4;
      -webkit-box-orient: vertical;
    }
    &-text {
      display: block;
      padding: rem(16px 18px);
      color:cl(title);
      font-size: rem(16px);
      line-height: rem(26px);
      min-height: rem(84px);
    }
  }
}