/* banner start */
.banner {
  background: cl(blue-dark);
  overflow: hidden;
  transition: all .5s ease;
  .container {
    position: relative;
    z-index: 6;
  }
  &--home {
    padding: rem(132px 0 0);
    @include media-breakpoint-up(lg) {
      min-height: 100vh;
    }
    @include media-breakpoint-down(md) {
      height: auto;
    }
    @include media-breakpoint-down(sm) {
      padding: 5rem 0 0;
    }
    .shapes {
      z-index: 5;
      @include media-breakpoint-down(xs) {
        top:auto;
        bottom: 0;
      }
      &-base {
        &--one {
          left:0;
          top:0;
          @include media-breakpoint-down(xs) {
            top:auto;
            bottom: 0;
          }
        }
        &--two {
          bottom:rem(-80px);
          left:rem(-100px);
          @include media-breakpoint-down(xs) {
            display: none;
          }
        }
      }
    }
    .decor {
      left:35%;
      bottom: rem(130px);
    }
    .decor--2 {
      left:1%;
      bottom: rem(230px);
      @include media-breakpoint-down(md) {
        left: auto;
        right:1%;
      }
    }
    .circles {
      left:48%;
      bottom: 40%;
    }
  }
  &--page {
    padding: rem(165px 0 25px);
    @include media-breakpoint-down(sm){
      padding-top: 6.3rem;
    }
    h1 {
      font-size: rem(50px);
      line-height: rem(60px);
      margin: rem(0 0 10px);
      font-weight: 700;
      color:white;
      @include media-breakpoint-down(xs) {
        font-size: rem(32px);
        line-height: rem(42px);
      }
    }
    .breadcrumb {
      padding: 0;
      margin: 0;
      background: none;
      &-item {
        font-size: rem(14px);
        color:white;
        &:before {
          color:white;
        }
        a {
          color:white;
        }
        &.active {
          color:cl(primary);
        }
      }
    }
  }
  &--big {
    padding: rem(220px 0 110px);
    position: relative;
    font-size: rem(20px);
    line-height: rem(30px);
    letter-spacing: rem(1px);
    color:white;
    text-align: center;
    @include media-breakpoint-down(sm) {
      padding: rem(120px 0 30px);
    }
    h1 {
      font-size: rem(50px);
      line-height: rem(60px);
      margin: rem(0 0 25px);
      font-weight: 700;
      @include media-breakpoint-down(xs) {
        font-size: rem(32px);
        line-height: rem(42px);
      }
    }
    .shapes {
      z-index: 5;
      @include media-breakpoint-down(xs) {
        top:auto;
        bottom: 0;
      }
      &-base {
        &--one {
          left:0;
          top:rem(100px);
          @include media-breakpoint-down(xs) {
            top:auto;
            bottom: 0;
          }
        }
        &--two {
          left: auto;
          top:auto;
          bottom:rem(-80px);
          right:rem(-100px);
          @include media-breakpoint-down(xs) {
            display: none;
          }
        }
      }
    }
    .decor {
      left:5%;
      bottom: 15%;
    }
    .circles {
      right:5%;
      bottom: 15%;
    }
  }
  &--plans {
    padding: rem(170px 0 200px);
    @include media-breakpoint-down(sm) {
      padding: rem(120px 0 170px);
    }
  }
  &--thanks {
    padding: rem(170px 0 20px);
    color:#9c9db1;
    font-size: rem(18px);
    @include media-breakpoint-down(sm) {
      padding: rem(110px 0 20px);
    }
    @include media-breakpoint-down(xs) {
      font-size: rem(16px);
    }
    h1 {
      font-size: rem(50px);
      line-height: rem(70px);
      font-weight: normal;
      color:white;
      strong {
        font-weight: 700;
      }
      @include media-breakpoint-down(xs) {
        font-size: rem(32px);
        line-height: rem(42px);
      }
    }
  }
  &--education {
    font-size: rem(18px);
    line-height: rem(26px);
    @include media-breakpoint-down(sm) {
      font-size: rem(15px);
      line-height: rem(20px);
      padding: rem(110px 0 50px);
    }
    h1 {
      font-size: rem(40px);
      line-height: rem(50px);
      margin: rem(0 0 20px);
      @include media-breakpoint-down(xs) {
        font-size: rem(30px);
        line-height: rem(40px);
        margin: rem(0 0 15px);
      }
    }
  }
  &--price {
    font-size: rem(18px);
    line-height: rem(26px);
    color:white;
    position: fixed;
    top:-100%;
    left:0;
    padding: rem(110px 0 15px);
    width:100%;
    z-index: 100;
    transition: all .5s linear;
    @include media-breakpoint-down(sm) {
      font-size: rem(15px);
      line-height: rem(20px);
      padding: rem(220px 0 50px);
    }
    @include media-breakpoint-down(xs) {
      padding: rem(200px 0 20px);
    }
    &.show {
      top:0;
    }
    h2 {
      font-size: rem(40px);
      line-height: rem(50px);
      margin: rem(0 0 20px);
      font-weight: 700;
      @include media-breakpoint-down(sm) {
        font-size: rem(30px);
        line-height: rem(40px);
        margin: rem(0 0 15px);
      }
      @include media-breakpoint-down(xs) {
        font-size: rem(20px);
        line-height: rem(30px);
        margin: 0;
      }
    }
  }
  &-back {
    position: absolute;
    left:0;
    bottom: 0;
    width:100%;
    padding: rem(0 0 18px);
    text-align: left;
    font-size: rem(15px);
    a {
      color:white;
      font-weight: 700;
      &:hover {
        text-decoration: none;
        color:cl(primary);
      }
    }
  }
  &-purchase {
    text-align: center;
    &-price {
      font-size: rem(26px);
      margin: rem(0 0 10px);
      span {
        text-decoration: line-through;
        color:#9c9db1;
        font-size: rem(16px);
      }
    }
    .btn {
      @include media-breakpoint-down(xs) {
        font-size: rem(15px);
        padding: 0;
        width:100%;
      }
    }
  }
  &-inner {
    height: 100%;
    position: relative;
    @include media-breakpoint-up(lg) {
      min-height: calc(100vh - 8.25rem);
    }
  }
  &-txt {
    font-size: rem(22px);
    line-height: rem(30px);
    color:white;
    margin: rem(0 0 20px);
    @include media-breakpoint-down(xs) {
      font-size: rem(18px);
      line-height: rem(28px);
    }
    p {
      margin: 0;
    }
  }
  &-rating {
    font-size: rem(14px);
    line-height: rem(20px);
    color:white;
    i {
      color:cl(gold);
      font-size: rem(16px);
      vertical-align: top;
    }
    p {
      margin: 0;
    }
  }
  &-video {
    position: absolute;
    right: 0;
    top: 0;
    width: 100%;
    height: 100%;
    overflow: hidden;
    @include media-breakpoint-down(md) {
      position: relative;
      height: auto;
    }
    &:before {
      content: '';
      position: absolute;
      @extend .full;
      background:url(/img/banner-frame.png) no-repeat 50% 50%;
      //background-size: 100% 100%;
      background-size: cover;
      z-index: 2;
      @include media-breakpoint-down(md) {
        display: none;
      }
    }
    &-hold {
      position: absolute;
      right: 0;
      top: 0;
      width: 55%;
      height: 90%;
      background-repeat: no-repeat;
      background-position: 50% 50%;
      background-size: cover;
      @include media-breakpoint-down(md) {
        position: relative;
        width:100%;
        height: auto;
      }
    }
    video {
      display: block;
      width:100%;
      height: 101%;
      @include media-breakpoint-down(md) {
        width:100%;
        height: auto;
      }
      //position: absolute;
      //left: 0;
      //top: 0;
      //width: 100%;
      //height: 100%;
      //@include media-breakpoint-down(xs) {
      //  margin-left: 0;
      //  top: -20px;
      //  height: calc(100% + 20px);
      //}
      //@media #{$min-aspect169} {
      //  height: 400%;
      //  top: -150%;
      //}
      //@media #{$max-aspect169} {
      //  width: 400%;
      //  left: -150%;
      //}
    }
    video[poster]{
      object-fit: cover;
    }
    img {
      display: block;
      width:100%;
      height: 100%;
      object-fit: cover;
    }
  }
  &-text {
    position: relative;
    width:100%;
    height: 100%;
    padding: rem(0 180px);
    color:cl(white);
    z-index: 6;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    &--pb_lg {
      padding-bottom: rem(150px);
    }
    &--center {
      justify-content: center;
      .banner-text-top {
        padding: 1rem 0 4rem;
      }
    }
    @include media-breakpoint-up(lg) {
      min-height: calc(100vh - 8.25rem);
    }
    @include media-breakpoint-down(lg) {
      padding: 2rem 1.5625rem 0;
    }
    &-top {
      width:48%;
      padding: rem(100px 0 40px);
      @include media-breakpoint-down(md) {
        width:100%
      }
      @include media-breakpoint-down(md) {
        padding: 3.25rem 0 2.5rem;
      }
      @include media-breakpoint-down(sm) {
        padding: 1rem 0 2.5rem;
      }
    }
    .ttl,
    h1 {
      display: block;
      font-size: rem(50px);
      line-height: rem(70px);
      letter-spacing: rem(1px);
      margin: rem(0 0 25px);
      font-weight: 700;
      @include media-breakpoint-down(lg) {
        font-size: rem(36px);
        line-height: rem(50px);
      }
      @include media-breakpoint-down(sm) {
        font-size: 2rem;
        line-height: 2.5rem;
        letter-spacing: rem(.5px);
      }
    }
    .txt {
      display: block;
      font-size: rem(20px);
      line-height: rem(30px);
      letter-spacing: rem(1px);
      margin: rem(0 0 35px);
      @include media-breakpoint-down(lg) {
        font-size: rem(16px);
        line-height: rem(26px);
      }
      @include media-breakpoint-down(sm) {
        font-size: rem(15px);
        line-height: rem(24px);
        letter-spacing: rem(.5px);
      }
    }
    ul {
      list-style: none;
      padding: 0;
      li {
        position: relative;
        padding: rem(0 0 0 36px);
        font-size: rem(20px);
        line-height: rem(26px);
        margin-bottom: rem(19px);
        background: url(/img/list-mark.png) no-repeat 0 0.125rem;
        background-size: rem(20px 20px);
      }
    }
    .note {
      font-size: rem(16px);
      line-height: rem(20px);
      color:#9c9db1;
      &-stars {
        display: inline-block;
        vertical-align: middle;
        white-space: nowrap;
      }
    }
    p {
      margin: 0;
    }
    &-logos {
      font-size: rem(16px);
      color:#9c9db1;
      padding: rem(0 0 50px);
      &-list {
        display: flex;
        flex-wrap: wrap;
        gap:rem(20px);
        padding: rem(20px 0 0);
        @include media-breakpoint-down(xs) {
          gap:1rem;
          justify-content: space-between;
        }
      }
      &-item {
        height: rem(32px);
        width:rem(190px);
        background-size: contain;
        background-repeat: no-repeat;
        background-position: 0 50%;
        transition: all .5s ease;
        opacity: .6;
        &:hover {
          opacity: 1;
        }
        @include media-breakpoint-down(xs) {
          width:calc(25% - 1rem);
        }
      }
    }
  }
  &-image {
    position: absolute;
    right:0;
    bottom: rem(123px);
    //width:rem(1009px);
    //height: rem(737px);
    height: calc(100% - 7.6875rem);
    width:53%;
    //padding-top: 38%;
    overflow: hidden;
    -webkit-mask: url(/img/banner-image-mask.png);
    -webkit-mask-size: 100% 100%;
    -webkit-mask-position: center;
    -webkit-mask-repeat: no-repeat;
    z-index: 7;
    @include media-breakpoint-down(md) {
      position: relative;
      bottom: auto;
      height: 25rem;
      width:70%;
      margin-left: 30%;
      margin-bottom: -2rem;
    }
    @include media-breakpoint-down(xs) {
      height: 20rem;
      width:100%;
      margin-left: 0;
    }
    &-inner {
      position: absolute;
      left: 0;
      top: 0;
      width: 100%;
      height: 100%;
      background-repeat: no-repeat;
      background-size: cover;
      background-position: 50% 50%;
    }
  }
  &-start {
    position: absolute;
    left:0;
    width:100%;
    bottom: 0;
    background: cl(primary);
    color:white;
    padding: rem(34px 0);
    font-size: rem(20px);
    line-height: rem(28px);
    z-index: 6;
    @include media-breakpoint-down(md) {
      text-align: center;
      position: relative;
    }
    p {
      margin: 0;
    }
    h2 {
      display: block;
      font-size: rem(30px);
      line-height: rem(40px);
      letter-spacing: rem(1px);
      margin: rem(0 0 10px);
      font-weight: 700;
      @include media-breakpoint-down(lg) {
        //font-size: rem(36px);
        //line-height: rem(50px);
      }
      @include media-breakpoint-down(sm) {
        //font-size: 2rem;
        //line-height: 2.5rem;
        //letter-spacing: rem(.5px);
      }
    }
  }
  .trial {
    margin: rem(0 0 60px);
    &-form {
      @include media-breakpoint-down(lg) {
        //flex-wrap: wrap;
      }
    }
    &-txt {
      @include media-breakpoint-down(lg) {
        width:50%;
      }
      @include media-breakpoint-down(xs) {
        width:100%;
      }
    }
    .btn {
      @include media-breakpoint-down(lg) {
        width:50%;
        padding: 0;
      }
      @include media-breakpoint-down(xs) {
        width:100%;
      }
    }
  }
  &-play {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%,-50%);
    width:rem(88px);
    height: rem(88px);
    border-radius: 50%;
    padding: rem(9px);
    background: cl(white, .3);
    margin: rem(-30px 0 0 60px);
    box-shadow:rem(0 19px 14px cl(black, .16));
    z-index: 100;
    @include media-breakpoint-down(md) {
      top:16rem;
      margin: 0;
    }
    @include media-breakpoint-down(xs) {
      width:rem(70px);
      height: rem(70px);
      padding: rem(6px);
      top:8rem;
    }
    a {
      width:100%;
      height: 100%;
      border-radius: 50%;
      background: cl(white);
      display: flex;
      justify-content: center;
      align-items: center;
      color:#5f85e1;
      font-size: rem(18px);
      padding: rem(0 0 0 3px);
      transition: all .3s linear;
      &:hover {
        text-decoration: none;
        background: #5f85e1;
        color:white;
      }
    }
  }
  &-info {
    overflow: hidden;
    border-radius: rem(10px);
    color:white;
    font-size: rem(18px);
    line-height: rem(28px);
    margin: rem(0 0 35px);
    &-top {
      background: #2f4c9a url(/img/banner-top.png) no-repeat 50% 0;
      background-size: 100% auto;
      padding: rem(90px 20px 20px);
      h3 {
        color:white!important;
        text-align: center;
        text-transform: uppercase;
        font-size: rem(36px)!important;;
        font-weight: 700;
        margin: rem(0 0 20px)!important;;
      }
      ul {
        list-style: none;
        margin: rem(0 0 0 15px);
        li {
          padding: rem(0 0 0 40px);
          margin: rem(0 0 15px);
          line-height: rem(22px);
          position: relative;
          &:before {
            content: "";
            position: absolute;
            left:0;
            top:0;
            width:rem(22px);
            height: rem(22px);
            background: url(/img/mark-1.png) no-repeat;
            background-size: contain;
          }
        }
      }
    }
    &-bottom {
      background: #0e1a40;
      color:white;
      text-align: center;
      padding: rem(20px 35px 35px);
      &-note {
        display: block;
        text-align: center;
        font-size: rem(15px);
        color:#9c9db1;
        margin: rem(5px 0 0);
      }
    }
  }
  &-subscription {
    overflow: hidden;
    background: cl(blue-dark);
    background: radial-gradient(ellipse at center,  rgba(55,67,135,1) 0%,rgba(7,10,58,1) 70%,rgba(7,10,58,1) 100%);
    border-radius: rem(10px);
    color:white;
    text-align: center;
    font-size: rem(18px);
    line-height: rem(28px);
    margin: rem(0 0 35px);
    padding: rem(40px 40px 50px);
    position: relative;
    &:before{
      z-index: 1;
      width: 100%;
      height: 75%;
      opacity: 0.47;
      content: "";
      position: absolute;
      left: 0;
      top:0;
      background-image:  linear-gradient( -20deg, cl(white, 0.05) 0%, cl(white, 0.2) 50%, cl(white, 0.05) 100%);
      clip-path: polygon(0 0, 100% 100%, 100% 0);
    }
    &:after{
      z-index: 1;
      width: 70%;
      height: 78%;
      opacity: 0.47;
      content: "";
      position: absolute;
      right: 0;
      top:22%;
      background-image:  linear-gradient( -0deg, cl(white, 0.05) 0%, cl(white, 0.2) 50%, cl(white, 0.01) 100%);
      clip-path: polygon(100% 0, 0% 100%, 100% 100%);
    }
    &-inner {
      position: relative;
      z-index: 2;
    }
    &-logo {
      width:rem(70px);
      margin: rem(0 auto 30px);
    }
    h3 {
      color:white!important;
      text-align: center;
      text-transform: uppercase;
      font-size: rem(30px)!important;
      line-height: rem(34px)!important;
      font-weight: 700;
      margin: rem(0 0 35px)!important;
    }
  }
  &-try {
    overflow: hidden;
    position: relative;
    border-radius: rem(10px);
    background: cl(blue-dark);
    color:white;
    font-size: rem(18px);
    line-height: rem(28px);
    padding: rem(30px);
    box-shadow:rem(0 4px 35px cl(black, .16));
    margin: rem(40px 0);
    @include media-breakpoint-up(sm) {
      padding: rem(30px 30px 30px 120px);
    }
    &-logo {
      position: relative;
      margin: 0;
      width:rem(50px);
      height: auto;
      z-index: 5;
      @include media-breakpoint-up(sm) {
        position: absolute;
        width:rem(70px);
        top:rem(30px);
        left:rem(30px);
      }
      @include media-breakpoint-down(sm) {
        margin-bottom: 1.2rem!important;
      }
    }
    &-inner {
      display: flex;
      justify-content: space-between;
      align-items: center;
      gap:rem(20px);
      position: relative;
      z-index: 5;
      @include media-breakpoint-down(xs) {
        flex-direction: column;
        text-align: center;
      }
    }
    p {
      margin: 0!important;
    }
    h3 {
      color:white!important;
      font-size: rem(28px)!important;
      line-height: rem(34px)!important;
      font-weight: 700;
      margin: rem(0 0 10px)!important;
    }
    .circles {
      right:rem(-60px);
      top:rem(-70px);
    }
    .shapes {
      &-base {
        opacity: 1;
        &--one {
          background-color: #364488;
        }
        &--two {
          background-color: #273677;
        }
      }
    }
    .btn {
      font-size: rem(16px);
      padding: rem(0 20px);
    }
  }
  &-book {
    background: url(/img/banner-book.jpg) no-repeat 50% 50%;
    background-size: cover;
    padding: rem(50px 0);
    position: relative;
    .container {
      position: relative;
      z-index: 5;
    }
    .shapes {
      overflow: hidden;
      &-base {
        position: absolute;
        filter: blur(60px);
        opacity: 0.8;
        &--one {
          width: rem(500px);
          height: rem(500px);
          background-color: #fff;
          left:40%;
          top:-40%;
          animation: none;
          @include media-breakpoint-down(sm) {
            top:auto;
            bottom: -20%;
            left:-22%;
          }
        }
      }
      .decor {
        right: 5%;
        top: 30%;
        @include media-breakpoint-down(lg) {
          right: 2%;
          top: 5%;
        }
        @include media-breakpoint-down(xs) {
          top: auto;
          bottom:6%;
        }
      }
    }
    &-img {
      max-width:100%;
      margin-top: rem(-85px);
      @include media-breakpoint-down(md) {
        margin: 0;
      }
      @include media-breakpoint-down(sm) {
        max-width:80%;
        margin-bottom: 2rem;
      }
    }
    &-text {
      font-size: rem(18px);
      line-height: rem(28px);
      letter-spacing: rem(.4px);
      padding-left: rem(30px);
      @include media-breakpoint-down(sm) {
        text-align: center;
        padding-left: 0;
      }
      h3 {
        color:cl(title);
        font-size: rem(40px);
        line-height: rem(50px);
        font-weight: 700;
        margin: rem(0 0 20px);
      }
      p {

      }
    }
  }
  &-plan {
    overflow: hidden;
    position: relative;
    border-radius: rem(10px);
    background: cl(blue-dark);
    color:cl(gold);
    font-size: rem(16px);
    line-height: rem(26px);
    padding: rem(50px 30px);
    text-align: center;
    &-inner {
      position: relative;
      z-index: 5;
      @include media-breakpoint-down(xs) {
        flex-direction: column;
        text-align: center;
      }
    }
    .btn {
      white-space: normal;
    }
    p {
      margin: 0!important;
    }
    .shapes {
      &-base {
        &--three {
          width: rem(400px);
          height: rem(350px);
          bottom:rem(-80px);
          right:rem(-100px);
        }
      }
    }
    .decor {
      top: 17%;
      left: auto;

      &--left {
        left: 5%;
      }
      &--right {
        right: 5%;

      }
    }
  }
}
