.sidebar {
  font-size: rem(16px);
  .open-sidebar {
    display: none;
    @include media-breakpoint-down(md) {
      display: block;
      position: absolute;
      right:100%;
      top:0;
      padding: rem(5px 10px);
      white-space: nowrap;
      background: cl(primary);
      border:1px solid cl(primary);
      color:white;
      font-size: rem(16px);
      i {
        margin-left: rem(5px);
      }
    }
  }
  &--sticky {
    @include media-breakpoint-up(lg) {
      position: sticky;
      top:9.375rem;
      max-height: calc(100vh - 11.375rem);
      overflow: auto;
      padding: rem(20px 0 0);
    }
    @include media-breakpoint-down(md) {
      position: fixed;
      right:rem(-300px);
      top:8.1rem;
      height: calc(100% - 8.1rem);
      width:rem(300px);
      background: white;
      z-index: 100;
      padding: 1rem;
      transition: all .5s linear;
      &.show {
        right:0;
        box-shadow: rem(-10px 0 20px rgba(0,0,0,0.3));
      }
    }
  }
  &-title {
    font-size: rem(18px);
    line-height: rem(24px);
    color:cl(title);
    font-weight: 700;
    text-transform: uppercase;
    border-bottom: 1px solid #e7eaf1;
    margin: rem(0 0 20px);
    padding: rem(0 0 10px);
  }
  &-editors_picks {
    list-style: none;
    margin: rem(0 0 35px);
    li {
      display: flex;
      align-items: flex-start;
      gap:rem(17px);
      margin: rem(0 0 18px);
      h5 {
        font-weight: 700;
        color:cl(title);
        font-size: rem(16px);
        line-height: rem(26px);
        margin: rem(0 0 8px);
        height: rem(52px);
        overflow: hidden;
        text-overflow: ellipsis;
        display: -webkit-box;
        -webkit-line-clamp: 2;
        -webkit-box-orient: vertical;
        a{
          color:cl(title);
          &:hover {
            text-decoration: none;
            color:cl(primary);
          }
        }
      }
      .news-author {
        color:#4b5565;
        font-size: rem(12px);
        line-height: rem(16px);
      }
    }
    &-img {
      flex-shrink: 0;
      width:rem(130px);
      padding-top: 20.5%;
      background-repeat: no-repeat;
      background-size: cover;
      background-position: 50%;
      border-radius: rem(8px);
      position: relative;
      &:before {
        content: "";
        @extend .full;
        background: cl(black,.1);
      }
    }
  }
  &-cat_list {
    list-style: none;
    margin: rem(0 0 35px);
    li {
      padding: rem(0 0 0 40px);
      margin: rem(0 0 15px);
      line-height: rem(20px);
      position: relative;
      &:before {
        content: "";
        position: absolute;
        left:rem(5px);
        top:rem(3px);
        width:rem(14px);
        height: rem(14px);
        background: url(/img/mark-1.png) no-repeat;
        background-size: 100% 100%;
      }
      a {
        color:cl(text);
        &:hover {
          text-decoration: none;
          color:cl(primary);
        }
      }
    }
  }
  &-latest_posts {
    list-style: none;
    margin: rem(0 0 35px);
    li {
      margin: rem(0 0 18px);
      a {
        color:cl(text);
        &:hover {
          text-decoration: none;
          color:cl(primary);
        }
      }
    }
  }
  &-social {
    display: flex;
    gap:rem(24px);
    font-size: rem(18px);
    a {
      color:cl(title);
      &:hover {
        color:cl(primary);
      }
    }
  }
  &-subscribe {
    margin: rem(0 0 35px);
    h4 {
      font-size: rem(18px);
      line-height: rem(24px);
      color:cl(title);
      font-weight: 700;
      text-transform: uppercase;
      margin: rem(0 0 5px);
    }
    p {
      margin: rem(0 0 10px);
    }
    &-holder {
      position: relative;
    }
    &-txt {
      display: block;
      width:100%;
      border: 1px solid #f1f4f9;
      background: #f1f4f9;
      height: rem(50px);
      padding: rem(0 110px 0 20px);
      font-size: rem(16px);
      font-weight: 700;
      color:cl(text);
      border-radius: rem(5px);
      @include placeholder(#9c9db1);
      &:focus {
        outline: none;
        border-color:cl(primary);
      }
    }
    &-btn {
      position: absolute;
      top:rem(4px);
      right:rem(4px);
      line-height: rem(42px);
      border:0;
      width: rem(90px);
      padding: 0;
      font-size: rem(14px);
    }
    &_box {
      padding: rem(26px 30px);
      overflow: hidden;
      border-radius: rem(10px);
      color:white;
      font-size: rem(18px);
      line-height: rem(28px);
      margin: rem(0 0 35px);
      background: cl(blue-dark);
      position: relative;
      &:before {
        content: "";
        position: absolute;
        left:1rem;
        top:1rem;
        width: rem(250px);
        height: rem(250px);
        background-color: #344695;
        filter: blur(60px);
        opacity: 0.8;
      }
      &-inner {
        position: relative;
        z-index: 2;
      }
      h4 {
        font-size: rem(24px)!important;
        line-height: rem(34px)!important;
        font-weight: 700;
        margin: rem(0 0 20px)!important;
        color:white!important;
      }
      .form {
        &-group {
          margin: rem(0 0 22px);
        }
        &-control {
          height: rem(46px);
          background: #f1f4f9;
          border-color:#f1f4f9;
          font-weight: 700;
          @include placeholder(#9c9db1);
        }
      }
    }
  }
  &-contents {
    list-style: none;
    @include media-breakpoint-down(md) {
      overflow-y: auto;
      max-height: calc(100% - 3.375rem);
    }
    & > li {
      margin: rem(0 0 25px);
      font-size: rem(16px);
      line-height: rem(26px);
      &.current {
        & > a {
          color:cl(primary);
        }
        ul {
          display: block;
        }
      }
      & > a {
        color:cl(title);
        font-weight: 700;
        &:hover {
          text-decoration: none;
          color:cl(primary);
        }
      }
      ul {
        list-style: none;
        padding: rem(0 0 0 30px);
        li {
          margin: rem(10px 0 0);
          a {
            color:cl(title);
            &:hover {
              text-decoration: none;
              color:cl(black);
            }
            &.active {
              color:cl(primary);

            }
          }
        }
      }
    }
  }
}