.btn {
  @extend .inline-b;
  background: cl(primary);
  color:cl(white);
  font-size: rem(18px);
  line-height: rem(48px);
  font-family: $font;
  font-weight: 700;
  border: rem(1px solid cl(primary));
  cursor: pointer;
  border-radius: rem(5px);
  padding: rem(0 30px);
  letter-spacing: rem(.2px);
  outline: 0;
  white-space: nowrap;
  &:hover {
    border-color:cl(primary);
    background: cl(primary, 0.9);
    color:cl(white);
    text-decoration: none;
  }
  &:focus {
    box-shadow:none;
    color:cl(white);
  }
  &--primary-outline {
    background: none;
    color:cl(primary);
    border-color:cl(primary);
    &:hover,
    &:focus{
      color:cl(primary);
      background: cl(primary, 0.1);
    }
  }
  &--gray-outline {
    background: none;
    color:cl(gray);
    border-color:cl(gray);
    &:hover,
    &:focus {
      border-color:cl(gray);
      background: cl(gray, 0.1);
      color:cl(gray);
    }
  }
  &--gold-outline {
    background: none;
    color:cl(gold);
    border-color:cl(gold);
    &:hover,
    &:focus  {
      border-color:cl(gold);
      background: cl(gold, 0.1);
      color:cl(gold);
    }
  }
  &--secondary {
    background: cl(secondary);
    border-color:cl(secondary);
    &:hover,
    &:focus  {
      border-color:cl(secondary);
      background: cl(secondary, .8);
    }
  }
  &--blue {
    background: cl(blue-light);
    border-color:cl(blue-light);
    &:hover,
    &:focus{
      border-color:cl(blue-light);
      background: cl(blue-light, 0.9);
      color:cl(white);
    }
  }
  &--blue-outline {
    background: none;
    color:cl(blue-light);
    border-color:cl(blue-light);
    &:hover,
    &:focus{
      color:cl(blue-light);
      background: cl(blue-light, 0.1);
    }
  }
  &--black {
    background: cl(black);
    border-color:cl(black);
    &:hover {
      background: cl(black, .8);
    }
  }
  &--gradient {
    line-height: rem(50px);
    border:0;
    @include gradient-x($gradientFirst, $gradientSecond);
    &:hover,
    &:focus  {
      @include gradient-x($gradientSecond, $gradientFirst);
    }
  }
  &--gray {
    background: #f1f4f9;
    border-color:#f1f4f9;
    color:#4b5565;
    &:hover,
    &:focus  {
      color:#4b5565;
      background: #f8f8f8;
      border-color:#f1f4f9;
    }
  }
  &--sm {
    line-height: rem(44px);
    font-size: rem(16px);
    padding: rem(0 20px);
    font-weight: 600;
  }
  &--xs {
    line-height: rem(36px);
    font-size: rem(15px);
    padding: rem(0 15px);
  }
  &-cancel {
    display: block;
    text-transform: uppercase;
    font-size: rem(12px);
    font-weight: normal;
    line-height: rem(14px);
    margin: rem(-10px 0 10px);
  }
  &--icon {
    i {
      @extend .pos-centerY;
      left:rem(22px);
    }
  }
}