.about {
  padding: rem(60px 0 75px);
  position: relative;
  background: #fff url(/img/line-shadow-4.png) no-repeat 0 0;
  overflow: hidden;
  font-size: rem(16px);
  line-height: rem(26px);
  h2 {
    font-size: rem(40px);
    margin: rem(0 0 30px);
    font-weight: 700;
    color:cl(title);
  }
  &-main {
    position: relative;
    color:cl(text);
    padding: rem(0px 0 60px);
    .decor {
      bottom: 10%;
      right: rem(80px);
      @include media-breakpoint-down(md) {
        right: rem(30px);
        bottom: rem(30px);
      }
    }
  }
  &-names {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    grid-gap:rem(25px);
    padding: rem(10px 0 25px);
    @include media-breakpoint-down(xs) {
      grid-template-columns: repeat(2, 1fr);
    }
    &-item {
      background: #edf2f9;
      border-radius: rem(8px);
      padding: rem(20px 10px 20px 20px);
      color:#898e97;
      font-size: rem(14px);
      line-height: rem(20px);
      border-top:rem(2px solid #edf2f9);
      a {
        display: inline-block;
        &:hover {
          text-decoration: none;
          color:cl(secondary);
        }
      }
      i {
        font-size: rem(16px);
      }
      h3 {
        text-transform: uppercase;
        font-weight: 700;
        color:cl(title);
        font-size: rem(18px);
        margin: rem(0 0 10px);
      }
      p {
        margin: rem(0 0 10px);
      }
    }
  }
  &-section {
    position: relative;
    overflow: hidden;
    background: cl(blue-dark);
    padding: rem(80px 0 40px);
    color:white;
    .container {
      position: relative;
      z-index: 2;
    }
    h2 {
      color:cl(white);
      text-align: center;
      margin: rem(0 0 40px);
    }
    &-list {
      display: flex;
      justify-content: space-between;
      align-items: flex-start;
      flex-wrap: wrap;
    }
    &-item {
      width:25%;
      padding: rem(25px 20px 20px 30px);
      border-radius: rem(10px);
      color:#9c9db1;
      @include media-breakpoint-down(md) {
        width:50%;
        margin-bottom: 1rem;
      }
      @include media-breakpoint-down(xs) {
        width:100%;
      }
      &:hover {
        @include media-breakpoint-up(xl) {
          background: cl(white, .1);
          .about-section-item-txt {
            visibility: visible;
          }
        }
      }
      img {
        display: block;
        width:rem(60px);
        height: auto;
        margin: rem(0 0 30px);
      }
      h3 {
        color:white;
        font-size: rem(24px);
        line-height: rem(34px);
        margin: rem(0 0 10px);
        font-weight: 700;
      }
      &-txt {
        @include media-breakpoint-up(xl) {
          visibility: hidden;
        }
      }
    }
    .shapes {
      &-base {
        &--one {
          left:15%;
          top:rem(50px);
        }
        &--two {
          left: auto;
          top:auto;
          right:rem(-80px);
          bottom:rem(-110px);
        }
        //&--three {
        //  bottom:rem(-80px);
        //  right:rem(-100px);
        //}
      }
    }
    .decor {
      left: 7%;
      top: 10%;
      @include media-breakpoint-down(lg) {
        left: 5%;
        top: 5%;
      }
      @include media-breakpoint-down(xs) {
        bottom: auto;
        top:6%;
      }
    }
    .circles {
      right:5%;
      bottom: 10%;
      @include media-breakpoint-down(lg) {
        bottom: auto;
        top:5%;
      }
    }
  }
  &-contacts {
    text-align: center;
    padding: rem(60px 0 0);
    &-social {
      display: flex;
      justify-content: center;
      align-items: center;
      gap:rem(28px);
      padding: rem(10px 0 0);
      a {
        width:rem(50px);
        height: rem(50px);
        border-radius: 50%;
        display: flex;
        justify-content: center;
        align-items: center;
        border:1px solid cl(primary);
        font-size: rem(22px);
        &:hover {
          text-decoration: none;
          background: cl(primary);
          color:white;
        }
      }
    }
  }
}