.book {
  &-bg {
    background: #f3f6fb;
  }
  h2 {
    color:cl(title);
    font-size: rem(40px);
    line-height: rem(46px);
    margin: rem(0 0 20px);
    font-weight: 700;
    @include media-breakpoint-down(xs) {
      font-size: rem(30px);
    }
  }
  h3 {
    color:cl(title);
    font-size: rem(30px);
    line-height: rem(36px);
    margin: rem(0 0 20px);
    font-weight: 700;
    @include media-breakpoint-down(xs) {
      font-size: rem(26px);
    }
  }
  &-section {
    background: #fff url(/img/line-shadow-4.png) no-repeat;
    padding: rem(75px 0);
    font-size: rem(16px);
    line-height: rem(26px);
    position: relative;
    z-index: 10;
    .form {
      &-group {
        margin: rem(0 0 35px);
      }
    }
    &-image {
      @include media-breakpoint-up(xl) {
        margin-top: rem(-290px);
      }
      @include media-breakpoint-down(md) {
        margin-bottom: 2rem;
      }
      img {
        max-width:100%;
        height: auto;
      }
    }
  }
  &-about {
    background: #f3f6fb;
    font-size: rem(18px);
    line-height: rem(28px);
    padding: rem(60px 0);
    position: relative;
    @include media-breakpoint-up(lg) {
      margin: rem(0 0 75px);
    }
    &-image {
      @include media-breakpoint-up(xl) {
        margin-left: rem(-110px);
      }
      @include media-breakpoint-up(lg) {
        margin-bottom: rem(-150px);
      }
      @include media-breakpoint-down(md) {
        margin-bottom: 2rem;
      }
      img {
        max-width:100%;
        height: auto;
      }
    }
    .decor {
      left: 7%;
      top: 10%;
      @include media-breakpoint-down(lg) {
        left: 5%;
        top: 5%;
      }
      @include media-breakpoint-down(xs) {
        bottom: auto;
        top:6%;
      }
    }
  }
  &-info {
    font-size: rem(16px);
    line-height: rem(26px);
    padding: rem(80px 0 100px);
    &-image {
      position: relative;
      @include media-breakpoint-down(md) {
        margin-bottom: 2rem;
      }
      &-inner {
        height: rem(477px);
        position: relative;
        z-index: 3;
        overflow: hidden;
        border-radius: rem(7px);
        box-shadow:rem(0 4px 35px cl(black, .16));
        display: flex;
        justify-content: center;
        align-items: center;
        background-repeat: no-repeat;
        background-position: 50%;
        background-size: cover;
        &:before {
          content: "";
          @extend .full;
          background: cl(blue-dark,.52);
        }
        @include media-breakpoint-down(xs) {
          height: 23rem;
        }
      }
      &-graph {
        max-width:90%;
        height: auto;
        position: relative;
        z-index: 2;
      }
      &-logo {
        position: absolute;
        bottom: rem(20px);
        right:rem(20px);
        width:rem(60px);
        height: auto;
        z-index: 2;
      }
      .shapes {
        &-base {
          position: absolute;
          filter: blur(60px);
          opacity: 0.8;
          &--one {
            border-radius: 100%;
            width: rem(300px);
            height: rem(300px);
            background-color: #333f82;
            left: -3rem;
            top: -2rem;
            animation: none;
          }

          &--two {
            border-radius: 100%;
            width: rem(150px);
            height: rem(150px);
            bottom: auto;
            top: 6rem;
            left: 14.75rem;
            background-color: #4a579b;
            animation: none;
          }
        }
      }
    }
    .decor {
      right: rem(-80px);
      bottom: rem(-70px);
      @include media-breakpoint-down(lg) {
        display: none;
      }
    }
  }
  &--page {
    background: #fff url(/img/line-shadow-4.png) no-repeat;
  }
  &-title {
    padding: rem(50px 0 40px);
    font-size: rem(18px);
    line-height: rem(28px);
    h3 {
      font-size: rem(40px);
      font-weight: normal;
      @include media-breakpoint-down(xs) {
        font-size: rem(28px);
        margin: rem(0 0 10px);
      }
    }
  }
  &-plan {
    padding: rem(70px 0 40px);
    font-size: rem(16px);
    line-height: rem(26px);
    h2 {
      font-size: rem(30px);
    }
  }
  &-include {
    font-size: rem(16px);
    line-height: rem(26px);
    padding: rem(40px 0);
    position: relative;
    h2 {
      text-align: center;
      margin: rem(0 0 45px);
    }
    &-grid {
      display: grid;
      grid-template-columns: repeat(3, 1fr);
      grid-gap: rem(40px 50px);
      @include media-breakpoint-down(xs) {
        grid-template-columns: repeat(1, 1fr);
      }
    }
    &-item {
      img {
        display: block;
        height: rem(50px);
        width:auto;
        margin: rem(0 0 15px);
      }
      h3 {
        font-size: rem(24px);
        margin: rem(0 0 10px);
        color:cl(title);
      }
    }
    .decor {
      left: auto;
      right: 5%;
      top: auto;
      bottom: 50%;
      @include media-breakpoint-down(lg) {
        right: 3%;
        bottom: 4%;
      }
    }
  }
  &-block {
    padding: rem(95px 0 50px);
    position: relative;
    overflow: hidden;
    background: white url(/img/line-shadow.png) no-repeat 100% 0;
    @include media-breakpoint-down(xs) {
      padding: 2.5rem 0 1.5rem;
    }
    .decor {
      left:5%;
      bottom:20%;
    }
    &-item {
      background: cl(blue-dark);
      border-radius: rem(8px);
      position: relative;
      overflow: hidden;
      display: flex;
      gap:rem(35px);
      align-items: center;
      padding: rem(25px);
      margin-bottom: rem(30px);
      &:before {
        content: "";
        position: absolute;
        filter: blur(60px);
        opacity: 0.8;
        border-radius: 100%;
        width: rem(300px);
        height: rem(300px);
        background-color: #212d6f;
        left:0;
        top:0;
        z-index: 1;
      }
      @include media-breakpoint-down(xs) {
        flex-direction: column;
        justify-content: center;
        text-align: center;
        padding: rem(32px);
      }
    }
    &-category {
      display: inline-block;
      margin: rem(0 0 25px);
      background: cl(white,.8);
      color:cl(title);
      font-weight: 700;
      text-transform: uppercase;
      font-size: rem(14px);
      line-height: rem(16px);
      padding: rem(7px 14px);
      border-radius: rem(3px);
    }
    &-img {
      width:rem(185px);
      flex-shrink: 0;
      height: auto;
      position: relative;
      z-index: 5;
    }
    &-hold {
      position: relative;
      z-index: 5;
    }
    h4 {
      font-weight: 700;
      color:white;
      font-size: rem(24px);
      line-height: rem(36px);
      margin: rem(0 0 25px);
    }
  }
}