.news {
  padding: rem(60px 0 75px);
  position: relative;
  background: #f3f6fb url(/img/line-shadow-6.png) no-repeat 0 0;
  overflow: hidden;
  font-size: rem(16px);
  line-height: rem(26px);
  &--inner {
    background: #fff url(/img/line-shadow-4.png) no-repeat 0 0;
  }
  &-article {
    font-size: rem(16px);
    line-height: rem(26px);
    @include media-breakpoint-down(md) {
      margin-bottom: 2rem;
    }
    &-top {
      display: flex;
      justify-content: space-between;
      align-items: center;
      gap:rem(10px);
      @include media-breakpoint-down(md) {
        flex-direction: column;
      }
    }
    .news-category {
      span {
        background: #f2f5fa;
      }
    }
    &-back {
      border-top:1px solid #e7eaf1;
      padding: rem(32px 0 0);
      margin: rem(32px 0 0);
    }
    h2 {
      font-size: rem(30px);
      line-height: rem(40px);
      color:cl(title);
      font-weight: 700;
      margin: rem(0 0 20px);
    }
    img {
      display: block;
      width:100%;
      height: auto;
      margin: rem(0 0 20px);
    }
    p {
      margin: rem(0 0 20px);
    }
    h3 {
      font-size: rem(26px);
      margin: rem(0 0 20px);
      font-weight: 700;
      color:cl(title);
    }
    h4 {
      font-size: rem(24px);
      margin: rem(0 0 20px);
      font-weight: 700;
      color:cl(title);
    }
    h5 {
      font-size: rem(22px);
      margin: rem(0 0 20px);
      font-weight: 700;
      color:cl(title);
    }
    h6 {
      font-size: rem(20px);
      margin: rem(0 0 20px);
      font-weight: 700;
      color:cl(title);
    }
    ol, ul {
      margin: rem(0 0 20px);
      padding: rem(0 0 0 20px);
    }
    .news-title h2 {
      font-size: rem(30px);
      margin: rem(0 0 20px);
    }
    .news-item {
      .news-category {
        padding: rem(12px 16px 0)!important;
        span {
          font-size: rem(12px);
          padding: rem(2px 7px);
        }
      }
    }
    video{
      max-width: rem(1260px);
      display: block;
      width:100%;
      height: auto;
    }
    &-iframeVideo {
      position: relative;
      padding-bottom: 56.25%;
      height: 0;
      margin: 0 0 2rem;
      iframe {
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
      }
    }
    ul {
      list-style: none;
      padding: 0;
      li {
        padding: rem(0 0 0 30px);
        margin: rem(0 0 20px);
        position: relative;
        &:before {
          content: "";
          position: absolute;
          left:0;
          top:rem(3px);
          width:rem(19px);
          height: rem(19px);
          background: url(/img/mark-1.png) no-repeat;
          background-size: 100% 100%;
        }
      }
    }
  }
  &-search {
    position: relative;
    margin: rem(0 0 35px);
    &-txt {
      display: block;
      width:100%;
      border:0;
      border-bottom: 1px solid #d2d2d2;
      background: none;
      height: rem(38px);
      padding: rem(0 40px 0 0);
      font-size: rem(16px);
      font-weight: 700;
      color:cl(text);
      border-radius: 0;
      @include placeholder(#9c9db1);
      &:focus {
        outline: none;
        border-color:cl(primary);
      }
    }
    &-btn {
      position: absolute;
      top:0;
      right:0;
      height: rem(38px);
      width: rem(38px);
      font-size: rem(18px);
      color:#4b5565;
      border:0;
      background: none;
    }
  }
  &-sort {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    align-items: flex-start;
    gap:rem(20px);
    padding: rem(0 0 30px);
    h3 {
      width:100%;
      text-align: center;
      font-size: rem(18px);
      color:cl(title);
      font-weight: 700;
      margin: 0;
    }
    &-item {
      color:#848f97;
      border: 1px solid #848f97;
      line-height: rem(44px);
      border-radius: rem(8px);
      width:rem(135px);
      font-weight: 700;
      text-align: center;
      &:hover {
        text-decoration: none;
        color:#848f97;
        background:cl(primary,.1);
      }
      &.active {
        border-color:cl(primary);
        color:cl(primary);
      }
    }
  }
  &-category {
    display: flex;
    flex-wrap: wrap;
    align-items: flex-start;
    width: 100%;
    overflow: hidden;
    gap:rem(5px);
    span {
      background: cl(white,.9);
      color:cl(title);
      font-weight: 700;
      text-transform: uppercase;
      font-size: rem(14px);
      line-height: rem(16px);
      padding: rem(6px 10px);
      border-radius: rem(3px);
    }
  }
  &-date {
    display: inline-block;
    font-size: rem(14px);
  }
  &-author {
    display: flex;
    justify-content: flex-start;
    align-items: center;
    gap:rem(13px);
    font-size: rem(14px);
    &-photo {
      width:rem(42px);
      height: rem(42px);
      border-radius: 50%;
      background-repeat: no-repeat;
      background-size: cover;
      background-position: 50%;
      flex-shrink: 0;
    }
    a {
      color:#4b5565;
      &:hover {

      }
    }
  }
  &-social {
    display: flex;
    gap:rem(24px);
    font-size: rem(18px);
    a {
      color:cl(title);
      &:hover {
        color:cl(primary);
      }
    }
  }
  &-view {
    display: flex;
    justify-content: flex-end;
    align-items: center;
    gap:rem(6px);
    margin: rem(0 0 26px);
    strong {
      font-size: rem(14px);
      margin-right: rem(12px);
    }
    button {
      width:rem(40px);
      height: rem(40px);
      border-radius: rem(3px);
      background: #f1f4f9;
      color:cl(blue-light);
      border:1px solid #f1f4f9;
      font-size: rem(18px);
      &:hover,
      &:focus {
        background: #f1f4f9;
        border-color:cl(blue-light);
      }
      &.active {
        background: cl(blue-light);
        border-color:cl(blue-light);
        color:cl(white);
      }
    }
  }
  &-grid {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    grid-gap:rem(30px);
    padding: rem(0 0 30px);
    @include media-breakpoint-down(xs) {
      grid-template-columns: repeat(1, 1fr);
    }
    &--sm {
      @include media-breakpoint-up(sm) {
        grid-template-columns: repeat(2, 1fr);
      }
    }
    &--list {
      grid-template-columns: repeat(1, 1fr);
      @include media-breakpoint-down(lg) {
        grid-gap:rem(20px);
      }
      .news-item {
        display: flex;
        align-items: center;
        gap:rem(42px);
        box-shadow:rem(0 9px 35px cl(black, .14));
        border-radius: rem(10px);
        background: cl(white);
        background: cl(white);
        font-size: rem(15px);
        line-height: 1.3;
        color:cl(gray);
        margin: rem(0 0 20px);
        @include media-breakpoint-down(xs) {
          gap:1rem;
        }
        .news-category {
          @include media-breakpoint-down(xs) {
            display: none;
          }
        }
        &-image {
          width:rem(420px);
          padding-top: 15%;
          margin: 0!important;
          border-radius: rem(10px);
          position: relative;
          flex-shrink: 0;
          @include media-breakpoint-down(lg) {
            width:25rem;
            padding-top: 23%;
          }
          @include media-breakpoint-down(xs) {
            width:10rem;
            padding-top: 30%;
          }
          &-link {

          }

        }
        &-play {
          @include media-breakpoint-down(xs) {
            width:rem(50px);
            height: rem(50px);
            padding: rem(5px);
            a {
              font-size: rem(16px);
            }
          }
        }
        &-holder {
          padding-right: rem(50px);
          @include media-breakpoint-down(xs) {
            padding-right: rem(10px);
          }
          p {
            display: block;
            display: -webkit-box;
          }
        }
      }
    }
  }
  &-item {
    background: #fff;
    border-radius: rem(8px);
    overflow: hidden;
    color:#4b5565;
    font-size: rem(15px);
    line-height: rem(24px);
    box-shadow:rem(0 4px 35px cl(black, .16));
    &--video {
      background: none;
      box-shadow: none;
      overflow: visible;
      .news-item-image {
        border-radius: rem(8px);
        box-shadow:rem(0 4px 35px cl(black, .16));
        margin: rem(0 0 20px);
        &--lg {
          margin-bottom: rem(35px);
        }
      }
    }
    &-play {
      @extend .pos-center;
      width:rem(70px);
      height: rem(70px);
      border-radius: 50%;
      padding: rem(7px);
      background: cl(white, .3);
      box-shadow:rem(0 19px 14px cl(black, .16));
      z-index: 100;
      a {
        width:100%;
        height: 100%;
        border-radius: 50%;
        background: cl(white);
        display: flex;
        justify-content: center;
        align-items: center;
        color:#5f85e1;
        font-size: rem(18px);
        padding: rem(0 0 0 3px);
        transition: all .3s linear;
        &:hover {
          text-decoration: none;
          background: #5f85e1;
          color:white;
        }
      }
    }
    &-image {
      padding-top: 53%;
      overflow: hidden;
      position: relative;
      background: black;
      &--lg {
        padding-top: 62%;
      }
      &-link {
        @extend .full;
        background-repeat: no-repeat;
        background-size: cover;
        background-position: 50%;
        opacity: .9;
      }
    }
    .news-category {
      position: absolute;
      top:0;
      left:0;
      padding: rem(17px 25px 5px);
    }
    .news-date {
      margin: rem(0 0 10px);
    }
    &-inner {
      padding: rem(15px 20px 20px);
    }
    &-holder {
      p {
        display: none;
        overflow: hidden;
        text-overflow: ellipsis;
        -webkit-line-clamp: 3;
        -webkit-box-orient: vertical;
        line-height: calc(26 / 16);
        @include media-breakpoint-down(xs) {
          -webkit-line-clamp: 1;
        }
      }
    }
    h2, h3 {
      font-weight: 700;
      color:cl(title);
      font-size: rem(18px);
      line-height: calc(26 / 18);
      margin: rem(0 0 10px);
      //height: rem(52px);
      overflow: hidden;
      text-overflow: ellipsis;
      display: -webkit-box;
      -webkit-line-clamp: 2;
      -webkit-box-orient: vertical;
      a{
        color:cl(title);
        &:hover {
          text-decoration: none;
          color:cl(primary);
        }
      }
    }
    &-detail {
      font-size: rem(18px);
      line-height: calc(32 / 18);
      h2, h3 {
        font-size: rem(24px);
      }
    }
    &-text {
      margin: rem(0 0 17px);
      font-size: rem(15px);
      line-height: rem(24px);
      height: rem(48px);
      overflow: hidden;
      text-overflow: ellipsis;
      display: -webkit-box;
      -webkit-line-clamp: 2;
      -webkit-box-orient: vertical;
    }
    &-bottom {
      display: flex;
      justify-content: space-between;
      align-items: center;
      .news-item-date {
        margin: 0;
      }
    }
    &-more {
      color:cl(primary);
      font-size: rem(18px);
      &:hover {
        text-decoration: none;
        color:cl(secondary);
      }
    }
    p {
      margin: 0;
    }
  }
  &-text {
    .news-item {
      &-inner {
        h2 {
          font-size: rem(24px);
          line-height: rem(34px);
          height: rem(68px);
        }
      }
      &-text {
        height: rem(24px);
        -webkit-line-clamp: 1;
      }
      .news-date {
        margin: 0;
      }
    }
  }
  &-title {
    h2 {
      font-size: rem(40px);
      line-height: rem(46px);
      color:cl(title);
      margin: rem(0 0 25px);
      font-weight: 700;
      @include media-breakpoint-down(xs) {
        font-size: rem(30px);
        line-height: rem(36px);
      }
    }
  }
  &-view_all {
    font-weight: 600;
    font-size: rem(15px);
  }
  &-block {
    margin: rem(0 0 32px);
    &-image {
      padding-top: 69%;
      background-repeat: no-repeat;
      background-size: cover;
      background-position: 50%;
      border-radius: rem(8px);
      box-shadow:rem(0 4px 35px cl(black, .16));
      position: relative;
      overflow: hidden;
      @include media-breakpoint-down(md) {
        margin-bottom: 2rem;
      }
      &--video {
        &:before {
          content: "";
          @extend .full;
          background: cl(black, .17);
        }
      }
      &-play {
        @extend .pos-center;
        width:rem(70px);
        height: rem(70px);
        border-radius: 50%;
        padding: rem(7px);
        background: cl(white, .3);
        box-shadow:rem(0 19px 14px cl(black, .16));
        z-index: 100;
        a {
          width:100%;
          height: 100%;
          border-radius: 50%;
          background: cl(white);
          display: flex;
          justify-content: center;
          align-items: center;
          color:#5f85e1;
          font-size: rem(18px);
          padding: rem(0 0 0 3px);
          transition: all .3s linear;
          &:hover {
            text-decoration: none;
            background: #5f85e1;
            color:white;
          }
        }
      }
    }
    &-text {
      font-size: rem(16px);
      line-height: rem(26px);
      letter-spacing: rem(.4);
      h3 {
        font-weight: 700;
        color:cl(title);
        font-size: rem(26px);
        line-height: rem(36px);
        margin: rem(0 0 10px);
      }
      &-more {
        font-weight: 600;
        font-size: rem(15px);
        display: inline-block;
      }
    }
  }
  &-blog {
    position: relative;
    .decor {
      right:5%;
      top:40%;
    }
  }
  &-video {
    background: white url(/img/line-shadow-7.png) no-repeat;
    padding: rem(45px 0 0);
  }
}
.news-item{
  .news-item-image{
    .video__locked {
      position: absolute;
      width: 100%;
      height: 100%;
      background-color: cl(black,0.1);
      color: cl(white);
      top: 0;
      left: 0;
      display: flex;
      justify-content: flex-end;
      align-items: flex-start;
      padding-top: rem(20px);
      z-index: 101;
      padding-right: rem(20px);
      svg {
       // top: rem(20px);
      //  right: rem(20px);
        display: block;
        width: rem(20px);
        height: auto;
        fill: currentColor;
      }
    }
  }
}
