.faq {
  &--inner {
    padding: rem(60px 0 75px);
    position: relative;
    overflow: hidden;
    font-size: rem(16px);
    line-height: rem(26px);
    background: #fff url(/img/line-shadow-4.png) no-repeat 0 0;
    .decor {
      bottom: 40%;
      right: rem(80px);
      @include media-breakpoint-down(md) {
        right: rem(30px);
        bottom: rem(30px);
      }
    }
  }
  &-article {
    @include media-breakpoint-up(xl) {
      padding-right: rem(50px);
    }
  }
  .card {
    border:0;
    border-radius: 0;
    border-bottom: 1px solid #e7eaf1;
    margin: rem(0 0 25px);
    padding: rem(0 0 25px);
    background: none;
    &-header {
      padding: 0;
      border:0;
      background: none;
    }
    &-opener {
      font-size: rem(24px);
      line-height: rem(34px);
      color:cl(title);
      background: none;
      display: block;
      width:100%;
      text-align: left;
      border:0;
      position: relative;
      padding: rem(0 35px 0 0);
      &:focus {
        outline: none;
        color:cl(primary);
      }
      &:before {
        content: "";
        position: absolute;
        right:0;
        top:rem(16px);
        width:rem(24px);
        height: rem(2px);
        background: cl(primary);
      }
      &:after {
        content: "";
        position: absolute;
        right:rem(11px);
        top:rem(5px);
        height:rem(24px);
        width: rem(2px);
        background: cl(primary);
        opacity: 0;
      }
      &.collapsed {
        &:after {
          opacity: 1;
        }
      }
    }
    &-body {
      padding: rem(10px 0 0);
      font-size: rem(16px);
      line-height: rem(26px);
      p {
        margin: rem(0 0 5px);
      }
    }
  }
}