.plans {
  padding: rem(60px 0);
  margin: rem(-185px 0 0);
  position: relative;
  background: url(/img/line-shadow-5.png) no-repeat 0 150%;
  background-size: rem(830px auto);
  overflow: hidden;
  font-size: rem(16px);
  line-height: rem(26px);
  z-index: 7;
  &--bg-top {
    background-position: 0 11.5625rem;
  }
  h2 {
    font-size: rem(40px);
    margin: rem(0 0 30px);
    font-weight: 700;
    color:cl(title);
  }
  &-grid {
    display: grid;
    grid-gap: rem(15px);
    grid-template-columns: repeat(4, 1fr);
    padding: rem(0 0 40px);
    @include media-breakpoint-down(md) {
      grid-gap: rem(35px);
      grid-template-columns: repeat(2, 1fr);
    }
    @include media-breakpoint-down(xs) {
      grid-gap: rem(35px);
      grid-template-columns: repeat(1, 1fr);
    }
  }
  &-notes {
    list-style: none;
    li {
      text-transform: uppercase;
      font-weight: 600;
      font-size: rem(12px);
      line-height: rem(16px);
      color:#9c9db1;
      margin: rem(0 0 5px);
    }
  }
  &-item {
    border-radius: rem(20px);
    background: white;
    box-shadow:rem(0 19px 40px cl(black, .16));
    padding: rem(30px 16px 10px);
    color:cl(text);
    position: relative;
    &-status {
      position: absolute;
      left:0;
      top:rem(-20px);
      font-size: rem(18px);
      line-height: rem(36px);
      padding: rem(0 16px);
      border-radius: rem(18px 18px 18px 0);
      color:white;
      &--silver {
        @include gradient-x-three-colors(#a9a8ad, #b5b3ba, 50%, #a9a8ad);
      }
      &--gold {
        @include gradient-x-three-colors(#caa038, #dbb75e, 50%, #caa038);
      }
      &--platinum {
        @include gradient-x-three-colors(#6d8188, #788b92, 50%, #6d8188);
      }
      &--diamond {
        @include gradient-x-three-colors(#726a7f, #9186a3, 50%, #726a7f);
      }
    }
    &-period {
      display: block;
      font-size: rem(15px);
      color:#52595f;
      margin: rem(-10px 0 15px);
      text-align: center;
    }
    &-price {
      text-align: center;
      &--old {
        text-decoration: line-through;
        font-size: rem(15px);
        color:#9c9db1;
        margin-bottom: rem(15px);
      }
      &--new {
        font-size: rem(40px);
        line-height: rem(46px);
        color:cl(title);
        margin-bottom: rem(15px);
        span {
          font-size: rem(15px);
        }
      }
    }
    .btn {
      font-size: rem(16px);
    }
    &-list {
      list-style: none;
      padding: rem(25px 0 0);
      li {
        position: relative;
        padding: rem(0 0 0 25px);
        font-size: rem(13px);
        line-height: rem(18px);
        margin-bottom: rem(19px);
        background: url(/img/list-mark.png) no-repeat 0 0;
        background-size: rem(17px 17px);
        &.disabled {
          background: none;
          color:#c0c1cc;
        }
        &:after {
          content: "";
          display: block;
          clear: both;
        }
        .info {
          float: right;
        }
      }
    }
    &-info {
      width:rem(20px);
      height: rem(20px);
      border-radius: 50%;
      border:rem(2px solid #9c9db1);
      color:#9c9db1;
      float: right;
      text-align: center;
      line-height: rem(16px);
      font-size: rem(10px);
      &:hover {
        border-color:cl(primary);
        color:cl(primary);
      }
    }
    .info {
      display: inline-block;
      vertical-align: middle;
      position: relative;
      z-index: 2;
      &:hover {
        .info-tooltip {
          display: block;
        }
      }
      i {
        color:#9c9db1;
        font-size: rem(16px);
        cursor: pointer;
      }
      &-tooltip {
        position: absolute;
        bottom: calc(100% + .9375rem);
        right:rem(-72px);
        width:rem(160px);
        background: cl(black, .9);
        border-radius: rem(3px);
        box-shadow:rem(0 7px 16px cl(black, .23));
        padding: rem(15px 10px 15px 15px);
        font-size: rem(12px);
        line-height: rem(20px);
        color:white;
        display: none;
        @include media-breakpoint-down(lg) {
          right:rem(-20px);
        }
        &:before {
          content:"";
          position: absolute;
          top:100%;
          right:rem(66px);
          width: 0;
          height: 0;
          border-left: rem(15px solid transparent);
          border-right: rem(15px solid transparent);
          border-top: rem(15px solid cl(black, .9));
          @include media-breakpoint-down(lg) {
            right:rem(14px);
          }
        }
        p {
          margin: 0;
        }
      }
    }
  }
  &-sidebar {
    h2 {
      font-size: rem(40px);
      line-height: rem(60px);
      letter-spacing: rem(2px);
      margin: rem(0 0 30px);
      font-weight: 700;
      color:cl(title);
    }
  }
}