.testim {
  font-size: rem(16px);
  line-height: rem(26px);
  color:cl(text);
  padding: rem(60px 0 75px);
  position: relative;
  background: #f3f6fb url(/img/line-shadow-4.png) no-repeat 0 0;
  overflow: hidden;
  .decor--right {
    bottom: 40%;
    right: rem(80px);
    @include media-breakpoint-down(md) {
      right: rem(30px);
      bottom: rem(30px);
    }
  }
  .decor--left {
    bottom: 20%;
    left: rem(110px);
    @include media-breakpoint-down(md) {
      display: none;
    }
  }
  h2 {
    font-size: rem(40px);
    margin: rem(0 0 40px);
    font-weight: 700;
    color:cl(title);
  }
  &-grid {
    margin: rem(0 -16px);
    @include media-breakpoint-down(md) {
      margin: 0;
    }
  }
  &-item {
    width:calc(50% - 2rem);
    background: #fff;
    border-radius: rem(10px);
    padding: rem(20px 25px 15px);
    margin: rem(0 16px 32px);
    box-shadow:rem(0 9px 35px cl(black, .14));
    overflow: hidden;
    @include media-breakpoint-down(md) {
      margin-right: 0;
      margin-left: 0;
      width:100%;
    }
    &--main {
      padding: 0;
      margin-right: 0;
      margin-left: 0;
      width:100%;
    }
    &-inner {
      padding: rem(0 25px);
      h2 {
        font-size: rem(24px);
        line-height: rem(34px);
        font-weight: 700;
        color:cl(title);
        margin: rem(0 0 15px);
      }
    }
    &-profile {
      display: flex;
      align-items: center;
      margin: rem(0 0 20px);
      padding: rem(0 55px 0 0);
      background: url(/img/quote.png) no-repeat 100% 0;
      background-size: rem(55px auto);
      &--no-bg {
        padding: 0;
        background: none;
      }
      &-photo {
        flex-shrink: 0;
        width:rem(76px);
        height: rem(76px);
        margin: rem(0 22px 0 0);
        border-radius: rem(50%);
        background-size: cover;
        background-position: 50% 50%;
        background-repeat: no-repeat;
      }
      &-info {
        font-size: rem(14px);
        h3 {
          font-size: rem(18px);
          font-weight: 700;
          color:cl(title);
          margin: 0;
        }
        p {
          margin: 0;
        }
      }
    }
    &-video {
      padding-top: 56.25%;
      background-size: cover;
      background-position: 50% 50%;
      background-repeat: no-repeat;
      margin: rem(0 0 14px);
      position: relative;
      &-play {
        @extend .pos-center;
        border:rem(8px solid cl(white,.5));
        width:rem(70px);
        height: rem(70px);
        font-size: rem(15px);
        border-radius: 50%;
        span {
          width:100%;
          height: 100%;
          background: white;
          border-radius: 50%;
          display: flex;
          justify-content: center;
          align-items: center;
          padding: rem(0 0 0 3px);
        }
        &:hover {
          text-decoration: none;
          color:white;
          span {
            background: cl(primary);
          }
        }
      }
    }
    &-txt {
      font-size: rem(18px);
      line-height: rem(28px);
      letter-spacing: rem(.4px);
    }
  }
  &-section {
    background: #f3f6fb;
    padding: rem(65px 0 55px);
    position: relative;
    overflow: hidden;
    .decor {
      top: 19%;
      left: 11%;
      @include media-breakpoint-down(lg) {
        left:5%;
      }
      @include media-breakpoint-down(xs) {
        top: 27%;
      }
    }
    h2 {
      font-size: rem(40px);
      line-height: rem(60px);
      font-weight: 700;
      color:cl(title);
      margin: rem(0 0 40px);
      @include media-breakpoint-down(xs) {
        font-size: rem(30px);
        line-height: rem(40px);
      }
    }
    &-slider {
      margin: rem(0 -15px);
      z-index: 10;
      @include media-breakpoint-down(lg) {
        margin: 0;
        padding: rem(0 50px);
      }
      @include media-breakpoint-down(xs) {
        padding: rem(0 25px);
      }
      &-prev,
      &-next {
        position: absolute;
        width:rem(46px);
        height: rem(46px);
        border-radius: 50%;
        border:1px solid #b2b5b8;
        background: none;
        color:#b2b5b8;
        left:rem(-73px);
        top:rem(133px);
        @include media-breakpoint-down(lg) {
          left:0;
        }
        @include media-breakpoint-down(xs) {
          width:rem(32px);
          height: rem(32px);
          font-size: rem(14px);
          left:-.5rem;
        }
      }
      &-next {
        left:auto;
        right:rem(-73px);
        @include media-breakpoint-down(lg) {
          right:0;
        }
        @include media-breakpoint-down(xs) {
          right:-.5rem;
        }
      }
    }
    &-item {
      background: white;
      border-radius: rem(10px);
      padding: rem(23px 23px 23px 27px);
      display: flex;
      align-items: flex-start;
      justify-content: flex-start;
      gap:rem(25px);
      box-shadow:rem(0 5px 15px cl(black, .1));
      margin: rem(15px 15px 20px);
      position: relative;
      @include media-breakpoint-down(sm) {
        flex-direction: column;
      }
      h3 {
        font-size: rem(18px);
        font-weight: 700;
        color:cl(title);
        margin: 0;
      }
      .position {
        font-size: rem(14px);
        margin: rem(0 0 15px);
      }
      .text-scroll {
        height: rem(140px);
        @include media-breakpoint-down(sm) {
          overflow: auto;
        }
      }
      p {
        margin: 0;
      }
    }
    &-user {
      display: flex;
      align-items: center;
      gap:rem(20px);
      font-size: rem(18px);
      line-height: rem(28px);
    }
    &-inner {

    }
    &-quote {
      position: absolute;
      width:rem(53px);
      height: auto;
      right:rem(35px);
      top:rem(-14px);
    }
    &-photo {
      flex-shrink: 0;
      width:rem(90px);
      height: rem(90px);
      border-radius: 50%;
      background-repeat: no-repeat;
      background-position: 50% 50%;
      background-size: cover;
    }
    &-content {
      padding: rem(20px 0 0);
      font-size: rem(18px);
      line-height: rem(28px);
      letter-spacing: rem(.4px);
      @include media-breakpoint-down(sm) {
        padding: 0;
      }
    }
  }
}