.crypto {
  //padding: rem(60px 0 75px);
  position: relative;
  overflow: hidden;
  .decor {
    &--1 {
      bottom: rem(-25px);
      left: rem(-50px);
      @include media-breakpoint-down(lg) {
        left: rem(20px);
        bottom: rem(-80px);
      }
    }
    &--2 {
      bottom: 40%;
      right: rem(80px);
      @include media-breakpoint-down(md) {
        right: rem(30px);
        bottom: rem(30px);
      }
    }
  }
  &-info {
    background: white;
    padding: rem(65px 0);
    position: relative;
    font-size: rem(16px);
    line-height: rem(26px);
    letter-spacing: rem(.4px);
    &--bg_right {
      @include media-breakpoint-up(xl) {
        background: url(/img/line-shadow.png) no-repeat 100% 0;
      }
    }
    &--bg_left {
      @include media-breakpoint-up(xl) {
        background: url(/img/line-shadow-7.png) no-repeat 0 0;
      }
    }
    &--bg {
      background: #f3f6fb;
    }
    .decor {
      left: 5%;
      top: 10%;
    }
    h2 {
      font-size: rem(30px);
      margin: rem(0 0 20px);
      font-weight: 700;
      color:cl(title);
    }
    h3 {
      font-size: rem(26px);
      margin: rem(0 0 20px);
      font-weight: 700;
      color:cl(title);
    }
    h4 {
      font-size: rem(24px);
      margin: rem(0 0 20px);
      font-weight: 700;
      color:cl(title);
    }
    h5 {
      font-size: rem(22px);
      margin: rem(0 0 20px);
      font-weight: 700;
      color:cl(title);
    }
    h6 {
      font-size: rem(20px);
      margin: rem(0 0 20px);
      font-weight: 700;
      color:cl(title);
    }
    ol, ul {
      margin: rem(0 0 20px);
      padding: rem(0 0 0 20px);
    }
    p {
      margin: rem(0 0 25px);
    }
    .title {
      h2 {
        font-size: rem(40px);
        line-height: rem(60px);
        font-weight: 700;
        color:cl(title);
        margin: rem(0 0 40px);
        @include media-breakpoint-down(xs) {
          font-size: rem(30px);
          line-height: rem(40px);
        }
      }
    }
    &-custom_list {
      padding: rem(20px 0 0);
      ul {
        list-style: none;
        padding: 0;
        li {
          position: relative;
          padding: rem(0 0 0 36px);
          font-size: rem(18px);
          line-height: rem(22px);
          margin-bottom: rem(19px);
          background: url(/img/list-mark.png) no-repeat 0 0.125rem;
          background-size: rem(17px 17px);
        }
      }
    }
  }
  &-forecast {
    padding: rem(115px 0 60px);
    font-size: rem(16px);
    line-height: rem(26px);
    letter-spacing: rem(.4px);
    background: url(/img/line-shadow.png) no-repeat 100% 0;
    h2 {
      font-size: rem(40px);
      margin: rem(0 0 20px);
      font-weight: 700;
      color:cl(title);
    }
    &-slider {
      margin: rem(0 0 25px);
      @include media-breakpoint-down(md) {
        padding: 0 1.5rem;
      }
    }
    &-prev,
    &-next {
      @extend .pos-centerY;
      left:rem(-70px);
      width:rem(46px);
      height: rem(46px);
      border-radius: 50%;
      border:1px solid #b2b5b8;
      color:#b2b5b8;
      background: none;
      z-index: 200;
      &:hover {
        border-color:cl(primary);
        color:cl(primary);
      }
      &:focus {
        outline: none;
        border-color:cl(secondary);
        color:cl(secondary);
      }
      @include media-breakpoint-down(md) {
        left:-.5rem;
        width:rem(35px);
        height: rem(35px);
      }
    }
    &-next {
      left:auto;
      right:rem(-70px);
      @include media-breakpoint-down(md) {
        right:-.5rem;
      }
    }
    &-item {
      background: white;
      box-shadow:rem(0 5px 15px cl(black, .1));
      padding: rem(27px 23px 25px);
      border-radius: rem(5px);
      margin: rem(12px 12px 15px);
      h3 {
        font-size: rem(18px);
        line-height: rem(26px);
        margin: rem(0 0 15px);
        font-weight: 700;
        color:cl(title);
        height: rem(52px);
        overflow: hidden;
        text-overflow: ellipsis;
        display: -webkit-box;
        -webkit-line-clamp: 2;
        -webkit-box-orient: vertical;
      }
    }
    &-image {
      display: block;
      height: 0;
      padding-top: 50%;
      margin: rem(0 0 20px);
      background-repeat: no-repeat;
      background-size: cover;
      background-position: 50%;
    }
    &-more {
      display: inline-block;
      font-size: rem(15px);
      font-weight: 700;
      &:hover {
        text-decoration: none;
        color:cl(secondary);
      }
    }
    &-all {
      font-size: rem(15px);
      font-weight: 700;
    }
  }
}