.footer {
  background: cl(blue-dark);
  padding: rem(30px 0 30px);
  color:cl(white);
  &--inner {
    padding: rem(30px 22px 30px 100px);
    transition: padding .3s linear;
    @include media-breakpoint-up(xl) {
      padding-left: rem(340px);
    }
    @include media-breakpoint-down(xs) {
      padding-left: rem(77px);
    }
  }
  &--full {
    padding-left: rem(100px);
    @include media-breakpoint-down(xs) {
      padding-left: rem(77px);
    }
  }
  &-social {
    display: flex;
    justify-content: flex-end;
    align-items: center;
    gap:rem(30px);
    @include media-breakpoint-down(xs) {
      justify-content: center;
      margin-bottom: 1.5rem;
    }
    a {
      color:cl(white);
      &:hover {
        text-decoration: none;
        color:cl(primary);
        border-color:cl(primary);
      }
      i {
        font-size: rem(20px);
      }
    }
  }
  h5 {
    font-size: rem(18px);
    font-weight: 700;
    margin: rem(0 0 18px);
    @include media-breakpoint-down(xs) {
      margin: rem(0 0 10px);
    }
  }
  &-box {
    margin-bottom: rem(15px);
    &.show {
      .footer-box-open {
        transform: rotate(180deg);
      }
      .footer-box-list {
        display: block;
      }
    }
    &-open {
      display: inline-block;
      vertical-align: middle;
      background: none;
      border:0;
      color:white;
      font-size: rem(18px);
      padding: rem(5px);
      transition: all .3s ease;
    }
    &-list {
      @include media-breakpoint-down(xs) {
        display: none;
      }
    }
  }
  &-menu {
    list-style: none;
    li {
      font-size: rem(15px);
      line-height: rem(18px);
      margin: rem(0 0 8px);
      a {
        color:#9c9db1;
        &:hover {
          text-decoration: none;
          color:white;
        }
      }
    }
  }
  &-contacts {
    font-size: rem(15px);
    color:#9c9db1;
    line-height: rem(25px);
    a {
      color:#9c9db1;
      &:hover {
        text-decoration: none;
        color:white;
      }
    }
  }
  &-slogan {
    font-size: rem(18px);
    line-height: rem(22px);
    @include media-breakpoint-down(xs) {
      text-align: center;
    }
    p {
      margin: 0;
    }
  }
  &-logo {
    display: block;
    width:rem(310px);
    max-width:100%;
    @include media-breakpoint-down(xs) {
      margin:0 auto 1.5rem;
    }
    img {
      width:100%;
      height: auto;
    }
  }
  &-line {
    background: #20234e;
    height: 1px;
    margin: rem(15px 0 30px);
  }
  &-copy {
    display: block;
    font-size: rem(14px);
    @include media-breakpoint-down(xs) {
      text-align: center;
    }
  }
  &-disclaimer {
    color:#9c9db1;
    font-size: rem(12px);
    line-height: rem(20px);
    padding: rem(15px 0 0);
    @include media-breakpoint-down(xs) {
      text-align: center;
    }
    p {
      margin: 0;
    }
  }
}

