.start {
  background: #f3f6fb;
  height: rem(400px);
  position: relative;
  z-index: 11;
  @include media-breakpoint-down(lg) {
    height: auto;
    padding: 3rem 0 4rem;
  }
  &:before {
    @include media-breakpoint-up(xl) {
      content:"";
      position: absolute;
      left:0;
      top:0;
      width:100%;
      height: rem(26px);
      background: white;
    }
  }
  &--lg {
    padding: rem(0 0 90px);
    height: auto;
    .start-text {
      padding: rem(70px 0 0);
    }
  }
  &--md {
    padding: rem(0 0 40px);
    height: auto;
    .start-inner {
      margin: 0;
    }
  }
  .decor {
    right:5%;
    top:30%;
  }
  .container {
    position: relative;
    z-index: 2;
  }
  &-inner {
    margin: rem(0 0 -28px);
  }
  &-monitor {
    float: right;
    width:rem(732px);
    height: rem(425px);
    background: url(../../../../assets/images/wave/start-monitor.png) no-repeat;
    background-size: 100% 100%;
    padding: rem(26px 104px 58px 83px);
    @include media-breakpoint-down(lg) {
      float: none;
      margin: 0 auto 2rem;
    }
    @include media-breakpoint-down(xs) {
      width:25rem;
      height:12rem;
      padding: 1.2rem 4rem 2.5rem 3.2rem;
    }
    &-inner {
      width:100%;
      height: 100%;
      background-size: cover;
      background-repeat: no-repeat;
      background-position: 50% 50%;
    }
  }
  &-text {
    font-size: rem(18px);
    line-height: rem(28px);
    letter-spacing: rem(0.4px);
    @include media-breakpoint-down(xs) {
      text-align: center;
    }
    h2 {
      font-size: rem(40px);
      font-weight: 700;
      margin: rem(0 0 20px);
      color:cl(title);
      @include media-breakpoint-down(xs) {
        font-size: rem(30px);
      }
    }
    &-buttons {
      display: flex;
      gap:rem(20px);
      @include media-breakpoint-down(xs) {
        flex-direction: column;
      }
    }
  }
  .trial {
    &-inner {
      color:cl(text);
      .social-link {
        &:hover {
          background: cl(white, .9);
        }
        &--g {
          border-color:#bbbcbd;
          color:cl(title);
        }
        &--fb {
          border-color:cl(facebook);
          color:cl(facebook);
        }
        &--tw {
          border-color:cl(twitter);
          color:cl(twitter);
        }
      }
    }
  }
}