.contact {
  font-size: rem(16px);
  line-height: rem(28px);
  color:cl(text);
  padding: rem(60px 0 75px);
  position: relative;
  background: white url(/img/line-shadow-4.png) no-repeat 0 0;
  overflow: hidden;
  .decor {
    bottom: rem(200px);
    right: rem(100px);
    @include media-breakpoint-down(xs) {
      bottom: 1rem;
      right:1rem;
    }
  }
  &-text {
    font-size: rem(16px);
    line-height: rem(28px);
    color:cl(text);
    padding: rem(30px 0 0);
    @include media-breakpoint-down(md) {
      padding: rem(0 0 40px);
    }
  }
  &-form {
    border-radius: rem(10px);
    background: white;
    box-shadow:rem(0 9px 35px cl(black, .14));
    padding: rem(40px 40px 20px);
    position: relative;
    z-index: 2;
    @include media-breakpoint-down(xs) {
      padding: rem(30px 25px 20px);
    }
    h2 {
      font-size: rem(30px);
      margin: rem(0 0 5px);
      font-weight: 700;
      color:cl(title);
    }
  }
  &-social {
    display: flex;
    gap:rem(20px);
    a {
      font-size: rem(22px);
      .fa {
        &-facebook-f {color:cl(facebook);}
        &-twitter {color:cl(twitter);}
        &-instagram {color:cl(instagram);}
        &-youtube {color:cl(youtube);}
      }
    }
  }
}