.calculator {
  font-size: rem(16px);
  line-height: rem(26px);
  color:#4b5565;
  padding: rem(60px 0 75px);
  position: relative;
  overflow: hidden;
  background: #f3f6fb url(/img/line-shadow-6.png) no-repeat 0 0;
  .decor {
    &--1 {
      bottom: rem(-25px);
      left: rem(-50px);
      @include media-breakpoint-down(lg) {
        left: rem(20px);
        bottom: rem(-80px);
      }
    }
    &--2 {
      bottom: 40%;
      right: rem(80px);
      @include media-breakpoint-down(md) {
        right: rem(30px);
        bottom: rem(30px);
      }
    }
  }
  &-article {
    background: white;
    border-radius: rem(10px);
    padding: rem(40px 40px 20px);
    box-shadow:rem(0 6px 13px cl(black, .08));
    color:cl(title);
    margin: rem(0 0 38px);
    position: relative;
    z-index: 2;
    h2 {
      font-size: rem(30px);
      color:cl(title);
      font-weight: 700;
      margin: rem(0 0 20px);
    }
  }
}