.static {
  font-size: rem(16px);
  line-height: rem(26px);
  color:#4b5565;
  padding: rem(60px 0 75px);
  position: relative;
  overflow: hidden;
  background: white url(/img/line-shadow-4.png) no-repeat 0 0;
  .decor {
    bottom: rem(150px);
    right: rem(150px);
    @include media-breakpoint-down(xs) {
      bottom: 1rem;
      right:1rem;
    }
  }
  &-text {
    padding: rem(0 40px 0 0);
    line-height: rem(26px);
    p {
      margin: rem(0 0 20px);
    }
    h2 {
      font-size: rem(24px);
      margin: rem(0 0 20px);
      font-weight: 700;
      color:cl(title);
    }
    h3 {
      font-size: rem(22px);
      margin: rem(0 0 20px);
      font-weight: 700;
      color:cl(title);
    }
    h4 {
      font-size: rem(20px);
      margin: rem(0 0 20px);
      font-weight: 700;
      color:cl(title);
    }
    h5 {
      font-size: rem(18px);
      margin: rem(0 0 20px);
      font-weight: 700;
      color:cl(title);
    }
    h6 {
      font-size: rem(16px);
      margin: rem(0 0 20px);
      font-weight: 700;
      color:cl(title);
    }
    ol, ul {
      margin: rem(0 0 20px);
      padding: rem(0 0 0 20px);
    }
    img {
      display: block;
      margin: 0 auto 2rem;
      max-width:90%;
      height: auto;
    }
  }
}