$font: 'Montserrat', sans-serif;
$fontPoppins: 'Poppins', sans-serif;
$fontBradleyHandBold: 'Bradley Hand Bold', sans-serif;
$fontOswald: 'Oswald', sans-serif;

$min-aspect169 : "(min-aspect-ratio: 16/9)";
$max-aspect169 : "(max-aspect-ratio: 16/9)";

$colors_theme : (
        "primary"   : "primary",
        "secondary" : "secondary",
        "back" : "back",
        "label" : "label",
        "text" : "text",
        "title" : "title",
        "gray" : "gray",
        "black" : "black",
        "blue-dark" : "blue-dark",
        "blue-light" : "blue-light",
        "white" : "white",
        "yellow" : "yellow",
        "gold" : "yellow",
        "green" : "green",
        "facebook" : "facebook",
        "twitter" : "twitter",
        "instagram" : "instagram",
        "linkedin" : "linkedin",
        "youtube" : "youtube",
        "google" : "google",
        "red" : "red"
);

$colors : (
        'primary'  : #2960c4,
        'secondary'  : #e35143,
        'back'  : #f1f4f9,
        'label'  : #4b5565,
        'text'  : #52595f,
        'title'  : #1b2329,
        'gray'  : #848f97,
        'blue-dark'  : #070a3a,
        'blue-light'  : #4185f4,
        'white' : #FFFFFF,
        'black' : #000000,
        'yellow' : #f7c744,
        'gold' : #efbc3d,
        'green' : #61bc46,
        'facebook' : #506aab,
        'twitter' : #5aafdc,
        'instagram' : #97498d,
        'linkedin' : #007ab9,
        'youtube' : #d84d43,
        'google' : #EA4335,
        'red' : #e35143,
);

$gradientFirst: #2960c4;
$gradientSecond: #1342a4;

.color {
  @each $status, $color in $colors {
    &--#{$status} {
      color: $color!important;
    }
  }
}

@function HexToRGB($hex) {
  @return red($hex), green($hex), blue($hex);
}

@function cl($name, $opacity: false) {
  @if $opacity {
    @return rgba(var(--color-#{unquote($name)}--rgb), $opacity);
  } @else {
    @return var(--color-#{unquote($name)});
  }
}

:root{
  @if $colors {
    @if $colors_theme {
      @each $key, $value in $colors_theme {
        --color-#{$key} : var(--color-#{$value});
        --color-#{$key}--rgb : var(--color-#{$value}--rgb);
      }
    }

    @each $key, $value in $colors {
      --color-#{$key} : #{$value};
      --color-#{$key}--rgb : #{HexToRGB($value)};
    }
  }
}