.promotion {

  padding: rem(90px 0 75px);
  position: relative;
  background: #fff;
  overflow: hidden;
  font-size: rem(16px);
  line-height: rem(26px);
  @include media-breakpoint-up(xl) {
    background: #fff url(/img/line-shadow-5.png) no-repeat 0 0;
    background-size: rem(530px auto);
  }
  .decor {
    bottom: 10%;
    right: rem(80px);
    @include media-breakpoint-down(md) {
      right: rem(30px);
      bottom: -2.125rem;
    }
  }
  &-box {
    background: #fff;
    border:3px solid cl(primary);
    border-radius: rem(16px);
    position: relative;
    margin: rem(0 0 80px);
    z-index: 3;
    &-present {
      position: absolute;
      top:rem(-76px);
      left:rem(46px);
      padding: rem(0 13px 0 8px);
      background: white;
      @include media-breakpoint-down(xs) {
        display: none;
      }
      img {
        width:rem(260px);
        height: auto;
      }
    }
    &-ttl {
      text-transform: uppercase;
      font-size:rem(30px);
      line-height: rem(32px);
      letter-spacing: rem(1px);
      font-weight: 600;
      padding: rem(0 16px);
      background: white;
      top:rem(-16px);
      @extend .pos-centerX;
    }
    &-inner {
      display: flex;
      justify-content: space-between;
      align-items: center;
      gap:rem(10px);
      padding: rem(20px 50px 6px 50px);
      position: relative;
      z-index: 2;
      @include media-breakpoint-down(md) {
        flex-direction: column;
        align-items: flex-start;
        gap:rem(30px);
        padding: rem(20px 50px 30px 50px);
      }
      @include media-breakpoint-down(xs) {
        text-align: center;
        align-items: center;
        padding: rem(40px 20px 30px 20px);
      }
    }
    &-name {
      margin: rem(0 0 -13px);
      padding: rem(0 0 0 60px);
      display: flex;
      flex-direction: column;
      justify-content: flex-start;
      align-items: flex-start;
      @include media-breakpoint-down(xs) {
        margin: 0;
        padding: 0;
        align-items: center;
        justify-content: center;
      }
      &-line {
        text-transform: uppercase;
        font-family: $fontOswald;
        font-weight: 700;
        background: white;
        &_1 {
          font-size:rem(57px);
          line-height: rem(57px);
          @include media-breakpoint-down(xs) {
            font-size:rem(40px);
            line-height: rem(50px);
          }
        }
        &_2 {
          font-size:rem(87px);
          line-height: rem(87px);
          @include media-breakpoint-down(xs) {
            font-size:rem(60px);
            line-height: rem(70px);
          }
        }
      }
    }
    &-text {
      font-size: rem(40px);
      line-height: rem(50px);
      color:cl(title);
      font-weight: 700;
      margin:0;
    }
    &-btn {
      .btn {

      }
    }
  }
  &-grid {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    grid-gap:rem(54px 45px);
    padding: rem(0 90px 50px);
    position: relative;
    z-index: 3;
    @include media-breakpoint-down(lg) {
      padding-right: 0;
      padding-left: 0;
    }
    @include media-breakpoint-down(xs) {
      grid-template-columns: repeat(1, 1fr);
    }
  }
  &-item {
    border-radius: rem(20px);
    background: white;
    box-shadow:rem(0 19px 40px cl(black, .16));
    padding: rem(70px 32px 32px);
    color:cl(text);
    position: relative;
    .plans-item-status {
      font-size: rem(40px);
      line-height: rem(60px);
      color:white;
      padding: rem(0 30px);
      border-radius: rem(30px 30px 30px 0);
      font-weight: 700;
    }
    .plans-item-list li {
      margin-bottom: rem(16px);
    }
    .text-scroll {
      height: rem(222px);
      @include media-breakpoint-down(sm) {
        height: auto;
      }
    }
    &-button {
      position: absolute;
      top:rem(25px);
      right:rem(25px);
      @include media-breakpoint-down(xs) {
        position: static;
        text-align: center;
      }
    }
  }
}