.performance {
  padding: rem(50px 0 90px);
  position: relative;
  background: #fff;
  overflow: hidden;
  font-size: rem(16px);
  line-height: rem(26px);
  h2 {
    font-size: rem(30px);
    margin: rem(0 0 15px);
    font-weight: 700;
    color:cl(title);
  }
  .instruments {
    font-size: rem(24px);
    margin: rem(0 0 35px);
    color:cl(title);
  }
  .nav {
    &-main {
      &.nav-tabs {
        margin: rem(40px 0);
        padding: rem(10px 12px);
        border-radius: rem(35px);
        background: white;
        justify-content: space-between;
        gap:rem(5px);
        box-shadow:rem(0 4px 35px cl(black, .16));
        @include media-breakpoint-down(xs) {
          justify-content: flex-start;
          gap:rem(10px);
        }
      }
      .nav-item {
        margin: 0;
      }
      .nav-link {
        font-size: rem(18px);
        line-height: rem(20px);
        font-weight: 700;
        color:cl(primary);
        letter-spacing: rem(.4px);
        padding: rem(14px 22px);
        border-radius: rem(25px);
        &.active {
          color:white;
          @include gradient-x($gradientFirst, $gradientSecond);
        }
        @include media-breakpoint-down(md) {
          font-size: rem(15px);
          line-height: rem(18px);
          padding: rem(12px 15px);
        }
      }
    }
  }
  &-box {
    box-shadow:rem(0 4px 35px cl(black, .16));
    border-radius: rem(10px);

  }
  &-view {
    .nav {
      &-tabs {
        margin: 0;
        border:0;
        justify-content: flex-end;
        @include media-breakpoint-down(xs) {
          justify-content: flex-start;
          gap:rem(10px);
        }
      }
      &-item {
        margin: 0;
        &:first-child {
          .nav-link {
            border-radius: rem(3px 0 0 3px);
          }
        }
        &:last-child {
          .nav-link {
            border-radius: rem(0 3px 3px 0);
          }
        }
      }
      &-link {
        font-size: rem(14px);
        line-height: rem(34px);
        padding: rem(0 16px);
        font-weight: 700;
        background: #f1f4f9;
        border:0;
        color:#4b5565;
        letter-spacing: rem(.4px);
        &.active {
          color:white;
          background: var(--color-primary);
        }
        @include media-breakpoint-down(md) {
          font-size: rem(15px);
          line-height: rem(18px);
          padding: rem(12px 15px);
        }
      }
    }

    .btn {
      border-radius: rem(3px);
      font-size: rem(14px);
      line-height: rem(34px);
      padding: rem(0 16px);
      &:first-child {
        border-radius: rem(3px 0 0 3px);
      }
      &:last-child {
        border-radius: rem(0 3px 3px 0);
      }
    }
  }
  &-aggregate {
    list-style: none;
    margin: rem(0 0 50px);
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    grid-gap: rem(20px);
    @include media-breakpoint-down(xs) {
      grid-template-columns: repeat(1, 1fr);
    }
    li {
      display: flex;
      align-items: center;
      gap:rem(20px);
      font-size: rem(18px);
      img {
        width:rem(35px);
        height: auto;
      }
    }
  }
  &-forex {
    list-style: none;
    margin: rem(0 0 50px);
    display: grid;
    grid-template-columns: repeat(6, 1fr);
    grid-gap: rem(10px);
    @include media-breakpoint-down(md) {
      grid-template-columns: repeat(4, 1fr);
    }
    @include media-breakpoint-down(xs) {
      grid-template-columns: repeat(2, 1fr);
    }
    li {
      display: flex;
      gap:rem(7px);
      align-items: center;
      font-size: rem(15px);
      line-height: rem(18px);
      text-transform: uppercase;
      .flags {
        width:rem(21px);
        height: rem(17px);
        display: flex;
        justify-content: center;
        align-items: center;
        position: relative;
        img {
          width:rem(13px);
          height: auto;
          &:nth-last-child(2) {
            position: absolute;
            top:0;
            left:0;
          }
          &:nth-child(2) {
            position: absolute;
            bottom: 0;
            right:0;
          }
        }
      }
    }
  }
}