.modal-backdrop {
  background: cl(blue-dark);
  &.show {
    opacity: .95;
  }
}
.modal-video {
  .modal-dialog {
    max-width: rem(1024px);
    pointer-events: all;
    @include media-breakpoint-down(md) {
      max-width: 90%;
    }
    @include media-breakpoint-down(xs) {
      max-width: 100%;
    }
  }
  &-content {
    position: relative;
    width:100%;
  }
  &-holder {
    position: relative;
    padding-bottom: 56.25%;
    width:100%;
    height: 0;
    overflow: hidden;
    iframe {
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
    }
  }
  video {
    display: block;
    width:100%;
    height: auto;
  }
  .close {
    margin: 0;
    position: absolute;
    top: rem(-30px);
    right: rem(-30px);
    width: rem(18px);
    height: rem(18px);
    overflow: hidden;
    text-indent: -999px;
    background: url(/img/close.png) no-repeat 50% 50%;
    background-size: contain;
    padding: 0;
    opacity: 1;
    z-index: 10;
    cursor: pointer;
    @include media-breakpoint-down(md) {
      right: 0;
    }
  }
}
.modal-thanks {
  .modal-dialog {
    max-width: rem(500px);
  }
  .modal-content {
    padding: rem(65px 30px 80px);
    background: cl(blue-dark);
    position: relative;
    overflow: hidden;
    .decor {
      left: -5%;
      bottom: -34%;
    }
  }
  h5 {
    text-align: center;
    font-size: rem(26px);
    font-weight: 700;
    margin: 0;
    color:cl(white);
    width:100%;
  }
  .close {
    margin: 0;
    position: absolute;
    top: rem(20px);
    right: rem(20px);
    width: rem(18px);
    height: rem(18px);
    overflow: hidden;
    text-indent: -999px;
    background: url(/img/close.png) no-repeat 50% 50%;
    background-size: contain;
    padding: 0;
    opacity: 1;
    z-index: 10;
  }
}

.modal-login {
  .modal-dialog {
    max-width: rem(500px);
    &--lg {
      max-width: rem(550px);
    }
  }
  .modal-content {
    position: relative;
    overflow: hidden;
  }
  .login-box {
    padding: rem(40px);
    border-radius: rem((10px));
    @include media-breakpoint-down(xs) {
      padding: rem(30px);
    }
  }
  h4 {
    font-size: rem(30px);
    line-height: rem(34px);
    margin: rem(0 0 25px);
    font-weight: 700;
    color:cl(title);
  }
  h5 {
    font-size: rem(26px);
    line-height: 1.3;
    margin: rem(0 0 25px);
    font-weight: 700;
    color:cl(title);
  }
  .sub-ttl {
    font-size: rem(17px);
    color:cl(title);
    padding-bottom: rem(10px);
  }
  .close {
    margin: 0;
    position: absolute;
    top: rem(15px);
    right: rem(15px);
    width: rem(18px);
    height: rem(18px);
    overflow: hidden;
    text-indent: -999px;
    background: url(/img/close.png) no-repeat 50% 50%;
    background-size: contain;
    padding: 0;
    opacity: 1;
    z-index: 10;
  }
  .social-list {
    display: flex;
    gap:rem(14px);
    @include media-breakpoint-down(xs) {
      flex-direction: column;
    }
  }
}

