.modal-subscribe {
  .modal-dialog {
    max-width: rem(800px);
  }
  .modal-content {
    position: relative;
    overflow: hidden;
  }
  .login-box {
    padding: rem(40px);
    border-radius: rem((10px));
    @include media-breakpoint-down(xs) {
      padding: rem(30px);
    }
  }
  h5 {
    font-size: rem(30px);
    line-height: rem(34px);
    margin: rem(0 0 25px);
    font-weight: 700;
    color:cl(title);
    &.width-icon {
      display: flex;
      align-items: center;
      gap:rem(20px);
    }
    .icon {
      fill:cl(gray);
      width:rem(50px);
      height: rem(50px);
      flex-shrink: 0;
    }
  }
  .close {
    margin: 0;
    position: absolute;
    top: rem(15px);
    right: rem(15px);
    width: rem(22px);
    height: rem(22px);
    overflow: hidden;
    text-indent: -999px;
    background: url(/img/close.png) no-repeat 50% 50%;
    background-size: contain;
    padding: 0;
    opacity: 1;
    z-index: 10;
  }
}

.modal-plan {
  .modal-dialog {
    max-width: rem(920px);
  }
  .modal-content {
    position: relative;
    overflow: hidden;
    font-family: $fontPoppins;
    border-radius: rem(10px);
  }
  .modal-body {
    padding: 0;
  }
  .modal-title {
    padding: rem(10px 45px);
    h5 {
      font-size: rem(24px);
      margin: 0;
      font-weight: 600;
      color:cl(white);
      @include media-breakpoint-down(xs) {
        font-size: rem(20px);
      }
    }
    &--silver {
      @include gradient-x-three-colors(#a9a8ad, #b5b3ba, 50%, #a9a8ad);
    }
    &--gold {
      @include gradient-x-three-colors(#caa038, #dbb75e, 50%, #caa038);
    }
    &--platinum {
      @include gradient-x-three-colors(#6d8188, #788b92, 50%, #6d8188);
    }
    &--diamond {
      @include gradient-x-three-colors(#726a7f, #9186a3, 50%, #726a7f);
    }
  }
  .modal-inner {
    padding: rem(15px 45px 30px);
    font-size: rem(16px);
    line-height: 1.2;
  }
  .close {
    margin: 0;
    position: absolute;
    top: rem(13px);
    right: rem(15px);
    width: rem(18px);
    height: rem(18px);
    overflow: hidden;
    text-indent: -999px;
    background: url(/img/close-white.png) no-repeat 50% 50%;
    background-size: contain;
    padding: 0;
    opacity: 1;
    z-index: 10;
  }
}

.modal-image {
  .modal-dialog {
    max-width: 90%;
    @include media-breakpoint-down(xs) {
      max-width: 100%;
    }
  }
  .modal-content {
    border-radius: 0;
  }
  .modal-body {
    padding: 0;
  }
  .close {
    margin: 0;
    position: absolute;
    top: rem(-25px);
    right: rem(-25px);
    width: rem(20px);
    height: rem(20px);
    overflow: hidden;
    text-indent: -999px;
    background: url(/img/close-white.png) no-repeat 50% 50%;
    background-size: contain;
    padding: 0;
    opacity: 1;
    z-index: 10;
    @include media-breakpoint-down(xs) {
      right:0;
    }
  }
}