.theory {
  padding: rem(60px 0);
  font-size: rem(16px);
  line-height: rem(26px);
  color:#4b5565;
  letter-spacing: rem(.4px);
  background: url(/img/line-shadow-7.png) no-repeat 0 0;
  position: relative;

  .decor {
    right: 5%;
    top: 50%;
    @include media-breakpoint-down(lg) {
      display: none;
    }
  }
  &-article {
    @include media-breakpoint-up(xl) {
      padding-left: rem(15px);
    }
    p {
      margin: rem(0 0 20px);
    }
    h1 {
      font-size: rem(30px);
      margin: rem(0 0 20px);
      font-weight: 700;
      color:cl(title);
    }
    h2 {
      font-size: rem(28px);
      margin: rem(0 0 20px);
      font-weight: 700;
      color:cl(title);
    }
    h3 {
      font-size: rem(26px);
      margin: rem(0 0 20px);
      font-weight: 700;
      color:cl(title);
    }
    h4 {
      font-size: rem(24px);
      margin: rem(0 0 20px);
      font-weight: 700;
      color:cl(title);
    }
    h5 {
      font-size: rem(22px);
      margin: rem(0 0 20px);
      font-weight: 700;
      color:cl(title);
    }
    h6 {
      font-size: rem(20px);
      margin: rem(0 0 20px);
      font-weight: 700;
      color:cl(title);
    }
    ol, ul {
      margin: rem(0 0 20px);
      padding: rem(0 0 0 20px);
    }
    img {
      display: block;
      margin: 0 auto 2rem;
      max-width:100%;
      height: auto;
    }
  }
}