.form {
  &-group {
    margin:rem(0 0 20px);
    position: relative;
    .view-pass {
      position: absolute;
      bottom:rem(14px);
      right:rem(12px);
      color: cl(primary);
      line-height: rem(16px);
      background: none;
      border:0;
      cursor: pointer;
      &:hover{
        color: cl(primary);
      }
      &:focus {
        outline: none;
        color: cl(primary);
      }
      i{
        font-size: 1rem;
      }
      .fa-eye-slash{
        display: block;
      }
      .fa-eye{
        display: none;
      }
      &.active{
        .fa-eye-slash{
          display: none;
        }
        .fa-eye{
          display: block;
        }
      }
    }
  }
  &-apply {
    position: absolute;
    right:rem(10px);
    bottom: 0;
    height: rem(52px);
    background: none;
    border:0;
    color:cl(blue-light);
    font-size: rem(16px);
    font-weight: 700;
    &:hover,
    &:focus {
      color:cl(primary);
    }
  }
  &-label {
    display: block;
    font-weight: 700;
    color:cl(label);
    font-size: rem(16px);
    margin:rem(0 0 5px);
    font-family: $font;
  }
  &-control,
  .custom-select {
    display: block;
    width:100%;
    font-family: $font;
    height: rem(50px);
    border-radius: rem(3px);
    background: cl(back);
    border:rem(1px solid cl(back));
    padding:rem(0 16px);
    font-size:rem(16px);
    color:$black;
    @include placeholder(cl(text));
    &:focus {
      outline: none;
      box-shadow:none;
      background-color: cl(white);
      border-color:cl(primary);
    }
    &.custom-select-white{
      border:rem(1px solid cl(black,0.25));
    }
  }
  .form-control{
    &.form-control-white{
      border:rem(1px solid cl(black,0.25));
      background-color: cl(white);
    }
  }
  .custom-select {
    //background-image: url(/img/select-arrow.png);
    //background-repeat: no-repeat;
    //background-position: right .7rem center;
    //background-size: rem(14px auto);
    padding-right: rem(25px);
    background: url(/img/select-arrow.png) no-repeat right 0.7rem center/0.875rem auto, cl(back);
    &:focus{
      background: url(/img/select-arrow.png) no-repeat right 0.7rem center/0.875rem auto, cl(white);
    }
  }
  textarea.form-control {
    resize: none;
    padding-top: 1rem;
    padding-bottom: 1rem;
    height:rem(130px);
  }
  .btn {
    line-height: rem(50px);
  }
  &-check {
    padding: 0;
    margin: 0;
    position: relative;
    &-input-checkbox {
      width: rem(22px);
      height: rem(22px);
      position: absolute;
      z-index: 3;
      opacity: 0;
      left:0;
      top:0;
      @extend .inline-b;
      & + .form-check-label {
        font-weight: normal;
        @extend .inline-b;
        padding: rem(0 0 0 34px);
        margin: 0;
        position: relative;
        font-size: rem(15px);
        line-height: rem(22px);
        color:cl(text);
        &:before {
          content: '';
          position: absolute;
          left:0;
          top:0;
          width: rem(22px);
          height: rem(22px);
          border-radius: rem(3px);
          background: cl(white);
          border:rem(1px solid cl(gray));
        }
        &:after {
          content: '\f00c';
          position: absolute;
          top:0;
          left: 0;
          width: rem(22px);
          height: rem(22px);
          line-height: rem(22px);
          text-align: center;
          font-family: 'Font Awesome 5 Free';
          font-weight: 900;
          opacity: 0;
          font-size: rem(10px);
          color: cl(primary);
        }
      }
      &:checked {
        & + .form-check-label {
          &:before {
            //background: cl(primary);
            //border-color: cl(primary);
          }
          &:after {
            opacity: 1;
          }
        }
      }
    }
    &-input-radio {
      width: rem(22px);
      height: rem(22px);
      position: absolute;
      z-index: 3;
      opacity: 0;
      left:0;
      top:0;
      @extend .inline-b;
      & + .form-check-label {
        font-weight: normal;
        @extend .inline-b;
        padding: rem(0 0 0 34px);
        margin: 0;
        position: relative;
        font-size: rem(15px);
        line-height: rem(22px);
        color:cl(text);
        &:before {
          content: '';
          position: absolute;
          left:0;
          top:0;
          width: rem(22px);
          height: rem(22px);
          border:rem(1px solid cl(gray));
          border-radius: 50%;
          background: cl(white);
        }
      }
      &:checked {
        & + .form-check-label {
          &:before {
            border-color: cl(primary);
            border-width:rem(7px);
          }
        }
      }
    }
  }
  .cards-list {
    display: flex;
    align-items: center;
    gap:rem(4px);
    margin: rem(0 0 20px);
    img {
      width:rem(26px);
      height: auto;
    }
  }
  .card-choose {
    border:1px solid cl(blue-light);
    border-radius: rem(5px);
    background: cl(white);
    box-shadow:rem(0 5px 10px cl(black, .09));
    line-height: rem(48px);
    color: cl(blue-light);
    text-align: center;
    font-weight: 700;
    position: relative;
    margin: rem(0 0 20px);
    .marker {
      position: absolute;
      color:cl(white);
      top:rem(-10px);
      right:rem(-10px);
      font-size: rem(16px);
      line-height: 1;
      width:rem(27px);
      height: rem(27px);
      border-radius: 50%;
      display: flex;
      justify-content: center;
      align-items: center;
      background:cl(blue-light);
    }
    .icon {
      @extend .pos-centerY;
      left:rem(36px);
      font-size: rem(18px);
    }
  }
  .choose-method {
    display: flex;
    justify-content: space-between;
    align-items: center;
    flex-wrap: wrap;
    margin: rem(0 0 20px);
    width:100%;
    .item {
      width:48%;
      @include media-breakpoint-down(xs) {
        width:100%;
        margin: 0.5rem 0 0;
      }
      .rad {
        position: absolute;
        opacity: 0;
        + label {
          height:rem(50px);
          border:1px solid #d9dbdf;
          border-radius: rem(5px);
          display: flex;
          align-items: center;
          justify-content: center;
          position: relative;
          margin: 0;
          color:cl(text);
          strong {
            font-size:rem(16px);
          }
          .icon {
            @extend .pos-centerY;
            left:rem(15px);
            font-size:rem(18px);
          }
          .marker {
            position: absolute;
            top:rem(-12px);
            right:rem(-12px);
            color:cl(blue-light);
            font-size:rem(24px);
            line-height: rem(24px);
            .fa-check-circle {
              background: white;
              opacity: 0;
            }
          }
        }
        &:checked {
          + label {
            border-color:cl(blue-light);
            color:cl(primary);
            box-shadow:rem(0 5px 10px cl(black, .09));
            strong {
            }
            .fa-check-circle {
              opacity: 1;
            }
          }
        }
        &:focus {
          + label {
            border-color:cl(primary);
          }
        }
      }
    }
  }

  .choose-list {
    display: flex;
    justify-content: space-between;
    align-items: center;
    flex-wrap: wrap;
    margin: rem(0 0 30px);
    width:100%;
    &--sm {
      .item {
        width:48%!important;
      }
    }
    .item {
      width:31%;
      margin-bottom: rem(14px);
      position: relative;
      @include media-breakpoint-down(xs) {
        width:48%;
      }
      .rad {
        position: absolute;
        opacity: 0;
        + label {
          height:rem(50px);
          border:1px solid #dddfe3;
          border-radius: rem(5px);
          display: flex;
          align-items: center;
          justify-content: center;
          gap:rem(5px);
          position: relative;
          margin: 0;
          color:cl(label);
          font-size: rem(16px);
          font-weight: 700;
          cursor: pointer;
          @include media-breakpoint-down(xs) {
            font-size: rem(15px);
          }
          & > i {
            font-size: rem(20px);
          }
          img {
            filter: grayscale(100%);
            max-width:90%;
            max-height: 90%;
          }
          svg {
            width:rem(20px);
            height: auto;
            fill:currentColor;
            @include media-breakpoint-down(xs) {
              width:rem(16px);
            }
          }
          .marker {
            position: absolute;
            color:cl(white);
            top:rem(-10px);
            right:rem(-10px);
            font-size: rem(16px);
            line-height: 1;
            width:rem(27px);
            height: rem(27px);
            border-radius: 50%;
            display: flex;
            justify-content: center;
            align-items: center;
            background:cl(blue-light);
            opacity: 0;
          }
          .icon {
            @extend .pos-centerY;
            left:rem(15px);
            font-size:rem(18px);
          }
        }
        &:checked {
          + label {
            border-color:cl(blue-light);
            color:cl(blue-light);
            box-shadow:rem(0 5px 10px cl(black, .09));
            img {
              filter: grayscale(0);
            }
            .marker {
              opacity: 1;
            }
          }
        }
        &:focus {
          + label {
            border-color:cl(primary);
          }
        }
      }
    }
  }
}
