.login {
  &--base,
  &--trial {
    min-height: calc(100vh - 8.25rem);
    position: relative;
  }
  &--base {

    background: #fff url(/img/line-shadow-7.png) no-repeat 0 0;
    padding: rem(230px 0 90px);
  }
  &--trial {
    background: #f3f6fb url(/img/line-shadow-6.png) no-repeat 0 0;
    padding: rem(55px 0 90px);
  }
  .decor {
    left:3%;
    bottom: 20%;
    @include media-breakpoint-down(lg) {
      display: none;
    }
  }
  &-steps {
    display: flex;
    justify-content: center;
    gap:rem(10px);
    margin: rem(-20px 0 35px);
    &-item {
      position: relative;
      flex-shrink: 0;
      padding: rem(0 0 22px);
      &.active{
        span {
          border-color:cl(green);
        }
      }
      &.done {
        span {
          border-color:cl(green);
          background: cl(green);
          color:white;
          em {
            display: none;
          }
          i {
            display: block;
          }
        }
      }
      span {
        display: flex;
        justify-content: center;
        align-items: center;
        width:rem(40px);
        height: rem(40px);
        border-radius: 50%;
        margin: rem(0 auto 8px);
        color:#6d7480;
        border:rem(2px solid #dbdfe5);
        font-size: rem(16px);
        font-weight: 700;
        i {
          display: none;
        }
        em {
          font-style: normal;
        }
      }
      strong {
        @extend .pos-centerX;
        white-space: nowrap;
        bottom: 0;
        color:#6d7480;
        font-size: rem(14px);
        @include media-breakpoint-down(xs) {
          font-size: rem(12px);
          font-weight: 600;
        }
      }
    }
    &-line {
      background: #dbdfe5;
      height: rem(4px);
      width:rem(130px);
      flex-shrink: 0;
      border-radius: rem(2px);
      margin: rem(18px 0 0);
      @include media-breakpoint-down(xs) {
        width:6rem;
      }
    }
    .progress {
      background: #dbdfe5;
      height: rem(4px);
      width:rem(130px);
      flex-shrink: 0;
      border-radius: rem(2px);
      margin: rem(18px 0 0);
      @include media-breakpoint-down(xs) {
        width:6rem;
      }
      &-bar {
        background: cl(green);
      }
    }
  }
  &-box {
    background: white;
    border-radius: rem(15px);
    box-shadow:rem(0 9px 35px cl(black, .14));
    padding: rem(40px);
    font-size: rem(16px);
    line-height: rem(26px);
    @include media-breakpoint-down(xs) {
      padding: 2rem 1.5rem;
    }
    h2 {
      font-size: rem(24px);
      line-height: rem(34px);
      margin: rem(0 0 25px);
      font-weight: 700;
      color:cl(title);
    }
    h3 {
      font-size: rem(18px);
      line-height: rem(22px);
      margin: rem(0 0 10px);
      font-weight: 700;
      color:cl(title);
    }
    &-links {
      max-width:rem(416px);
      margin: 0 auto;
      color:#878d97;
      .btn {
        font-size: rem(16px);
      }
    }
    .form {
      &-group {
        margin: rem(0 0 25px);
      }
    }
    &-terms {
      font-size: rem(13px);
      line-height: rem(22px);
      ul {
        list-style: none;
        li {
          position: relative;
          padding: rem(0 0 0 13px);
          &:before {
            content: "";
            position: absolute;
            left:0;
            top:rem(8px);
            width:rem(6px);
            height: rem(6px);
            border-radius: 50%;
            background: cl(text);
          }
        }
      }
    }
  }
  &-group {
    position: relative;
    margin: rem(0 0 16px);
    &-radio {
      position: absolute;
      //top:rem(15px);
      //left:rem(24px);
      //width:rem(20px);
      //height: rem(20px);
      top:0;
      left:0;
      width:100%;
      height: 100%;
      cursor: pointer;
      opacity: 0;
      z-index: 3;
      & + label {
        position: absolute;
        width:100%;
        height: 100%;
        z-index: 1;
        background: #fafbfd;
        border:1px solid #eaebed;
        border-radius: rem(8px);
        &:before {
          content: "";
          position: absolute;
          top:rem(15px);
          left:rem(24px);
          width:rem(20px);
          height: rem(20px);
          border:1px solid #eaebed;
          background: white;
          border-radius: 50%;
        }
        &:after {
          content: '\f00c';
          position: absolute;
          top:rem(15px);
          left:rem(24px);
          width: rem(20px);
          height: rem(20px);
          line-height: rem(20px);
          text-align: center;
          font-family: 'Font Awesome 5 Free';
          font-weight: 900;
          opacity: 0;
          font-size: rem(10px);
          color: white;
        }
      }
      &:checked {
        & + label {
          background: white;
          border-color:cl(primary);
          box-shadow:rem(0 5px 10px cl(black, .09));
          &:before {
            border-color:cl(primary);
            background: cl(primary);
          }
          &:after {
            opacity: 1;
          }
        }
      }
    }
    &-inner {
      position: relative;
      z-index: 2;
      padding: rem(15px 25px 15px 60px);
      @include media-breakpoint-down(xs) {
        padding-left: rem(15px);
      }
      &-title {
        @include media-breakpoint-down(xs) {
          padding-left: rem(60px);
        }
      }
      h3 {
        font-size: rem(16px);
        font-weight: 600;
        margin: rem(0 0 5px);
      }
      .detail {
        display: block;
        font-size: rem(13px);
        padding: rem(0 0 5px);
      }
      p {
        margin: 0;
      }
    }
    &-item {
      margin: rem(8px 0 0);
      padding: rem(0 0 0 40px);
      font-size: rem(15px);
      line-height: rem(18px);
      position: relative;
      img {
        position: absolute;
        top:0;
        left:0;
        width:rem(22px);
        height: auto;
      }
      h4 {
        font-size: rem(16px);
        font-weight: 600;
        margin: rem(0 0 4px);
      }
    }
  }
  &-summary {
    background: white;
    box-shadow:rem(0 9px 35px cl(black, .14));
    padding: rem(26px);
    font-size: rem(16px);
    line-height: rem(25px);
    color:#4b5565;
    margin: rem(0 0 35px);
    border-radius: rem(10px);
    @include media-breakpoint-down(md) {
      //margin-top: 2rem;
    }
    h2 {
      font-size: rem(24px);
      font-weight: 700;
      color:cl(title);
      margin: rem(0 0 20px);
    }

  }
  .social-link {
    display: inline-block;
    vertical-align: top;
    position: relative;
    font-weight: 700;
    color:cl(title);
    line-height: rem(48px);
    border-radius: rem(3px);
    border:1px solid cl(white, .5);
    font-size: rem(16px);
    text-align: center;
    letter-spacing: 0;
    padding: rem(0 0 0 22px);
    @include media-breakpoint-down(xs) {
      //width:100%;
      //width:auto;
      //padding: rem(0 12px);
    }
    &:hover {
      text-decoration: none;
      background: cl(white, .1);
    }
    &--colored {
      &.social-link--fb {
        border-color:cl(facebook);
        background:cl(facebook);
        color:white;
        i {
          color:white;
        }
      }
      &.social-link--tw {
        border-color:cl(twitter);
        background:cl(twitter);
        color:white;
        i {
          color:white;
        }
      }
    }
    &--g {
      border-color:#bbbcbd;
      i {
        color:cl(google);
      }
    }
    &--fb {
      border-color:cl(facebook);
      color:cl(facebook);
      i {
        color:cl(facebook);
      }
    }
    &--tw {
      border-color:cl(twitter);
      color:cl(twitter);
      i {
        color:cl(twitter);
      }
    }
    img {
      width:rem(20px);
      height: auto;
    }
    i, img {
      font-size: rem(18px);
      @extend .pos-centerY;
      left:rem(18px);
    }
  }
  &-section {
    background: cl(blue-dark);
    color:white;
    padding: rem(80px 0 70px);
    text-align: center;
    position: relative;
    overflow: hidden;
    font-size: rem(18px);
    line-height: 1.3;
    h2, h3 {
      font-size: rem(40px);
      line-height: rem(60px);
      margin: rem(0 0 25px);
      font-weight: 700;
      @include media-breakpoint-down(xs) {
        font-size: rem(30px);
        line-height: rem(40px);
      }
    }
    &-tags {
      display: flex;
      justify-content: center;
      align-items: center;
      gap:rem(11px);
      flex-wrap: wrap;
      margin: rem(0 0 36px);
      span {
        background: white;
        color: cl(blue-dark);
        font-size: rem(14px);
        line-height: rem(28px);
        border-radius: rem(3px);
        font-weight: 700;
        padding: rem(0 10px);
      }
    }

    &-form {
      padding: rem(20px 0 0);
    }
    .form {
      &-group {
        margin: rem(0 0 20px);
        position: relative;
      }
      &-control,
      .custom-select {
        background-color: #fff;
        font-weight: 700;
        @include placeholder(#9c9db1);
      }
    }
    .container {
      position: relative;
      z-index: 3;
    }
    .shapes {
      &-base {
        &--two {
          bottom:auto;
          top:rem(-110px);
          left:rem(-80px);
        }
        &--three {
          bottom:rem(-80px);
          right:rem(-100px);
        }
      }
    }
    .decor {
      left: 5%;
      top: 10%;
      @include media-breakpoint-down(xs) {
        bottom: auto;
        top:6%;
      }
    }
    .circles {
      right:5%;
      top: 10%;
    }
  }
  &-info {
    margin: rem(0 0 30px);
    &.opened {
      .login-info-open {
        &:after {
          opacity: 0;
        }
      }
      .login-info-content {
        display: block;
      }
    }
    &-title {
      position: relative;
      padding: rem(0 30px 0 0);
      border-bottom: 1px solid #d3dae5;
      h4 {
        font-size: rem(24px);
        line-height: rem(34px);
        color:cl(title);
        margin: 0;
        font-weight: 700;
        display: flex;
        align-items: center;
        gap:1rem;
        @include media-breakpoint-down(xs) {
          font-size: rem(20px);
          line-height: rem(30px);
        }
        img {
          width:rem(16px);
          height: auto;
          flex-shrink: 0;
        }
      }

    }
    &-open {
      @extend .pos-centerY;
      right:0;
      width:rem(24px);
      height: rem(24px);
      background: none;
      border:0;
      &:before,
      &:after {
        content: "";
        position: absolute;
        background: cl(blue-light);
      }
      &:before {
        width:100%;
        height: rem(2px);
        left:0;
        top:rem(11px);
      }
      &:after {
        width:rem(2px);
        height: 100%;
        top:0;
        left:rem(11px);
      }
    }
    &-content {
      padding: rem(18px 0 0);
      display: none;
      .schedule {
        font-size: rem(14px);
        line-height: rem(24px);
      }
      .payment {
        display: flex;
        align-items: center;
        justify-content: center;
        flex-wrap: wrap;
        gap:rem(10px);
        margin: rem(0 0 20px);
        img {
          height: auto;
        }
        &-cards {
          img {
            width:rem(50px);
          }
        }
        &-methods {
          img {
            width:rem(140px);
          }
        }
      }
    }
    &-list {
      list-style: none;
      li {
        position: relative;
        padding: rem(0 0 0 38px);
        font-size: rem(16px);
        line-height: rem(20px);
        margin-bottom: rem(16px);
        background: url(/img/list-mark.png) no-repeat 0 1px;
        background-size: rem(17px 17px);
        color:#4b5565;
      }
    }
  }
}