.subscribe {
  background: white url(/img/line-shadow-3.png) no-repeat 100% 0;
  background-size: auto 100%;
  position: relative;
  z-index: 10;
  font-size: rem(18px);
  padding: rem(90px 0 65px);
  @include media-breakpoint-down(xs) {
    padding: 3.625rem 0 3.0625rem;
    background: white;
  }
  h2 {
    text-align: center;
    font-size: rem(40px);
    font-weight: 700;
    margin: rem(0 0 20px);
    color:cl(title);
  }
  &-form {
    padding: rem(20px 0 0);
  }
  .form {
    &-group {
      margin: rem(0 0 20px);
      position: relative;
    }
    &-control,
    .custom-select {
      display: block;
      width:100%;
      font-family: $font;
      height: rem(50px);
      border-radius: rem(5px);
      background: #f1f4f9;
      border:rem(1px solid #f1f4f9);
      padding:rem(0 16px 0 25px);
      font-size:rem(16px);
      color:$black;
      font-weight: 700;
      @include placeholder(#9c9db1);
      &:focus {
        outline: none;
        box-shadow:none;
        background: cl(white);
        border-color:cl(primary);
      }
    }
    .custom-select {
      background-image: url(/img/select-arrow.png);
      background-repeat: no-repeat;
      background-position: right .5rem center;
      background-size: rem(16px auto);
      padding-right: rem(25px);
    }
  }
  .container {
    position: relative;
    z-index: 2;
  }
  .decor {
    bottom: rem(50px);
    right:rem(290px);
    @include media-breakpoint-down(lg) {
      right:1rem;
    }
  }
}